jQuery.fn.dragDrop = function(options) {

    var defaults = {
        icon: "tqi-activity",
        title: "Activity",
        instructions: 'Drag and drop the draggable items into their correct buckets.',
        revert: true,
        revertDuration: 200,
        snap: '.dragDropBucket',
        snapMode: 'both',
        keepCorrectDropItem: false,
        highlightBucketOnDrop: false,
        disableBucketOnCorrectDrop: false,
        randomiseDraggableItems: true,
        randomiseBuckets: false,
        hoverClass: 'dragDropHoverClass',
        tolerance: 'pointer',
        iconFeedback: true,
        backgroundFeedback: true,
        feedback: "Congratulations, you've completed the activity. Click the reset button below to play again!",
        incorrectFeedback: "You got some answers incorrect, try again.",
        allCorrectMode: true,
        enablePagination: false,
        dropCount: 0,
        paginationItems: [],
        paginationPage: 1
    };

    var settings = $.extend( {}, defaults, options );

    var methods = {
        init: function(reset) {
            methods.createElements(reset)
            if (reset != true)
                methods.createHeader()
            methods.createDraggables()
            methods.createBuckets()
            if (settings.extras)
                methods.createExtras()
            methods.applyCSS()
            methods.addContent()
            methods.addClearFix()
            methods.addDragDrop()
            methods.bindEvents()
            methods.bindScrRdrEvents()

            if (settings.mode != 'freeform') {
                methods.setBucketDimensions()
            }

            if (settings.enablePagination) {
                methods.paginationSetup()
            }
        },


        createElements : function(reset) {
            settings.elements.header = $('<div class="drag-drop-header">')
            settings.elements.headerTitleIconWrapper = $('<div class="header-col-wrap"></div>')
            settings.elements.headerIcon = $('<i aria-hidden="true" class="tqi ' + settings.icon + ' tqi-2x tq-panel-icon pull-left"></i>') // Create TQ Icon
            settings.elements.headerTitle = $('<h3>' + settings.title + '</h3>') // Create H3 Element
            settings.elements.headerInstructions = $('<p>' + settings.instructions + '</p>') // Create Instructions Element

            settings.elements.wrap = $("<div class='col-md-12 col-sm-12 col-xs-12 dragDropActivity'>")
            settings.elements.draggableWrapper = $("<div class='col-xs-6 col-sm-3 col-md-3 dragDropVcenter'>")
            settings.elements.draggable = $("<div role='group' aria-labelledby='dragDropScrRdr_"+settings.elements.main.attr('id')+"'' tabindex='0' class='not-moved dragDropItem'>")
            settings.elements.itemClear = $("<div class='dragDropClear'>")
            settings.elements.bucketWrapper = $("<div class='col-xs-3 col-sm-3 col-md-3 dragDropVcenter'>")
            settings.elements.bucket = $("<div role='group' aria-labelledby='dragDropScrRdr_"+settings.elements.main.attr('id')+"'' tabindex='0' class='dragDropBucket'>")
            settings.elements.extra = $("<div aria-labelledby='dragDropScrRdr_"+settings.elements.main.attr('id')+"'' tabindex='0' class='dragDropExtra'>")
            settings.elements.popUp = $("<div class='dragDropOverlay'><div role='group' aria-labelledby='dragDropScrRdr_"+settings.elements.main.attr('id')+"'' tabindex='0' class='well dragDropPromptBox'><button class='dragDropResetBtn btn btn-default'>Reset</button></div></div>")
            settings.elements.freeformWrap = $("<div class='dragDropFreeFormWrap'>")
            settings.elements.freeFormImage = $("<img class='dragDropImage img-responsive'>")
            settings.elements.randomColorArrayLength = 54;
            settings.elements.randomColorArray = methods.getColors();
            settings.elements.scrRdr = $("<div role='group' aria-live='assertive' class='sr-only' id='dragDropScrRdr_"+ settings.elements.main.attr('id') +"'>")

            if (reset == true) {
                settings.elements.header.hide()
                settings.elements.wrap.hide()
                settings.elements.main.append(settings.elements.header)
                settings.elements.main.append(settings.elements.wrap)
                settings.elements.wrap.fadeIn()
                settings.elements.header.fadeIn()
            }
            else {
                settings.elements.main.append(settings.elements.header)
                settings.elements.main.append(settings.elements.wrap)
            }

        },

        getColors: function() {
            var colorArray = []
            for (var i = 1; i <= settings.elements.randomColorArrayLength; i++) {
                colorArray[i] = 'dragDropRandomColor' + i
            }
            return colorArray
        },

        createHeader: function() {
            settings.elements.header.append(settings.elements.headerTitleIconWrapper)
            settings.elements.headerTitleIconWrapper.append(settings.elements.headerIcon) // Apend TQ Icon
            settings.elements.headerTitleIconWrapper.append(settings.elements.headerTitle) // Append H3
            settings.elements.header.append($('<div class="clearfix"></div>'))
            settings.elements.header.append("<p aria-labelledby='dragDropScrRdr_"+settings.elements.main.attr('id')+"'' tabindex='0' class='dragDropInstructions'>" + settings.instructions + "</p>")
        },

        createDraggables : function() {
            if (settings.randomiseDraggableItems == true)
                methods.randomise(settings.items)
            if (settings.mode == 'freeform') {
                settings.elements.wrap.append(settings.elements.freeformWrap)
                for (var i = 0; i < settings.items.length; i++) {
                    settings.elements.freeformWrap.append(settings.elements.draggable.clone().attr('data-bucket',settings.items[i].bucketMatch))
                }
            }
            else {
                for (var i = 0; i < settings.items.length; i++) {
                    settings.elements.wrap.append(settings.elements.draggableWrapper.clone().append(settings.elements.draggable.clone().attr('data-bucket',settings.items[i].bucketMatch)))
                }
                settings.elements.wrap.append(settings.elements.itemClear)
                methods.adjustCols(settings.items.length,'.dragDropItem')
            }
        },

        createBuckets : function() {
            if (settings.randomiseBuckets == true)
                methods.randomise(settings.buckets)
            if (settings.mode == 'freeform') {
                settings.elements.wrap.append(settings.elements.freeformWrap)
                for (var i = 0; i < settings.buckets.length; i++) {
                    settings.elements.freeformWrap.append(settings.elements.bucket.clone().attr('data-bucket',i+1))
                }
            }
            else {
                settings.elements.wrap.append($("<div class='clearfix'></div><div class='drag-drop-bucket-margin'></div>"))
                for (var i = 0; i < settings.buckets.length; i++) {
                    settings.elements.wrap.append(settings.elements.bucketWrapper.clone().append(settings.elements.bucket.clone().attr('data-bucket',i+1)))
                }
                methods.adjustCols(settings.buckets.length,'.dragDropBucket')
            }
        },

        createExtras: function() {
            if (settings.mode == 'freeform') {
                for (var i = 0; i < settings.extras.length; i++) {
                    settings.elements.freeformWrap.append(settings.elements.extra.clone())
                }
            }
            else {
                for (var i = 0; i < settings.extras.length; i++) {
                    settings.elements.wrap.append(settings.elements.extra.clone())
                }
            }
        },

        applyCSS : function() {
            methods.shuffle(settings.elements.randomColorArray)
            settings.elements.main.find('.dragDropItem').each(function(i) {
                $(this).addClass(settings.elements.randomColorArray[i+1])
            });

            settings.elements.main.find('.dragDropItem').each(function(i) {
                if (settings.items[i].css != undefined && settings.mode != 'freeform')
                    $(this).css(settings.items[i].css)
                if (settings.items[i].css != undefined && settings.mode == 'freeform')
                    $(this).css(settings.items[i].css).css('position','absolute')
            });
            settings.elements.main.find('.dragDropBucket').each(function(i) {
                if (settings.buckets[i].css != undefined && settings.mode != 'freeform')
                    $(this).css(settings.buckets[i].css)
                if (settings.buckets[i].css != undefined && settings.mode == 'freeform')
                    $(this).css(settings.buckets[i].css).css('position','absolute')
            });
            settings.elements.main.find('.dragDropExtra').each(function(i) {
                if (settings.extras[i].css != undefined && settings.mode != 'freeform')
                    $(this).css(settings.extras[i].css)
                if (settings.extras[i].css != undefined && settings.mode == 'freeform')
                    $(this).css(settings.extras[i].css).css('position','absolute')
            });
            $(window).load(function() {
                if (settings.mode != 'freeform')
                    methods.setBucketDimensions()
            })

            if (settings.mode == 'freeform') {
                settings.elements.freeformWrap.css('width', settings.freeformWidth).css('height', settings.freeformHeight)
            }
        },

        adjustCols : function(itemOrBucket,str) {
            settings.elements.main.find(str).parent().each(function(i) {
                if (itemOrBucket == 1) {
                    removeAdd($(this),12)
                }
                else if (itemOrBucket == 2) {
                    removeAdd($(this),6)
                }
                else if (itemOrBucket % 4 == 1) {
                    if (i == itemOrBucket - 1 || i == itemOrBucket - 2) {
                        removeAdd($(this),6)
                    }
                    else if (i == itemOrBucket - 5 || i == itemOrBucket - 4 || i == itemOrBucket - 3) {
                        removeAdd($(this),4)
                    }
                }
                else if (itemOrBucket % 4 == 2) {
                    if (i == itemOrBucket - 1 || i == itemOrBucket - 2 || i == itemOrBucket - 3) {
                        removeAdd($(this),4)
                    }
                    else if (i == itemOrBucket - 6 || i == itemOrBucket - 5 || i == itemOrBucket - 4) {
                        removeAdd($(this),4)
                    }
                }
                else if (itemOrBucket % 4 == 3) {
                    if (i == itemOrBucket - 1 || i == itemOrBucket - 2 || i == itemOrBucket - 3) {
                        removeAdd($(this),4)
                    }
                }
            });

            function removeAdd(ctx,num) {
                ctx.removeClass('col-md-3 col-sm-3 col-xs-3')
                ctx.addClass('col-md-'+num+'').addClass('col-sm-'+num+'').addClass('col-xs-12')
            };
        },

        addContent : function() {
            loop('.dragDropItem', settings.items)
            loop('.dragDropBucket', settings.buckets)
            loop('.dragDropExtra', settings.extras)

            function loop(name,type) {
               settings.elements.main.find(name).each(function(i) {
                    if (type[i].image == undefined) {
                        $(this).html("<p>" + type[i].text + "</p>")
                        if (settings.mode != 'freeform')
                            $(this).addClass('dragDropBucketTweak')
                    }
                    else {
                        $(this).removeClass('label label-default')
                        $(this).html($("<img alt='"+type[i].imageAlt+"' src='"+type[i].image+"'>").addClass('img-responsive'))
                        $(this).addClass('dragDropBucketTweak')
                    }
                })
            }
        },

        addClearFix : function() {
            var count = 0;
            loop('.dragDropItem')

            function loop(name) {
                settings.elements.main.find(name).each(function(i) {
                    count++;
                    var str = $(this).parent()[0].className
                    var str2 = str.split('col-md-')
                    var cols = str2[1]
                    if (cols != undefined) {
                        if (cols[0] == 3 && count == 4 || cols[0] == 4 && count == 3 || cols[0] == 6 && count == 2) {
                            settings.elements.itemClear.clone().insertAfter($(this).parent())
                            count = 0;
                        }
                    }
                });
            }
        },

        addDragDrop: function() {
            settings.elements.main.find('.dragDropItem').draggable({
                containment: settings.elements.wrap,
                revert: settings.revert,
                revertDuration: settings.revertDuration,
                snapMode: settings.snapMode,
                snap: settings.snap,
                stack: '.dragDropItem',
                drag: function(event, ui) {
                    $('.dragDropItem').css('cursor','move')
                },
                stop: function(event, ui) {
                    $('.dragDropItem').css('cursor','pointer')
                },
            })

            settings.elements.main.find('.dragDropBucket').droppable({
                hoverClass: settings.hoverClass,
                tolerance: settings.tolerance,
                drop: function(event, ui) {
                    if (settings.enablePagination) {
                        if ($(ui.draggable).hasClass("not-moved")) {
                            settings.dropCount++;
                            methods.updatePagination()
                        }
                    }

                    $(ui.draggable).removeClass("not-moved")
                    var validBuckets = ui.draggable.data('bucket')
                    var targetBucket = $(this).data('bucket')
                    if (validBuckets == targetBucket || validBuckets.length > 1 && validBuckets.indexOf(targetBucket) > -1) {
                        if (settings.backgroundFeedback == true)
                            ui.draggable.removeClass('dragDropIncorrectItem').addClass('dragDropCorrectItem')
                        if (settings.iconFeedback == true)
                            ui.draggable.removeClass('dragDropIncorrectItemIcon').addClass('dragDropCorrectItemIcon')
                        ui.draggable.draggable('disable')
                        if (settings.disableBucketOnCorrectDrop == true)
                            $(this).droppable('disable')
                        if (settings.keepCorrectDropItem == true) {
                            $(ui.draggable).draggable({revert:false})
                            $(ui.draggable).offset($(this).offset());
                        }
                        if (settings.highlightBucketOnDrop == true) {
                            $(this).addClass('dragDropCorrectItem')
                        }
                        methods.checkAnswers()
                    } else {
                        if (settings.backgroundFeedback == true)
                            ui.draggable.removeClass('dragDropCorrectItem').addClass('dragDropIncorrectItem')
                        if (settings.iconFeedback == true)
                            ui.draggable.removeClass('dragDropCorrectItemIcon').addClass('dragDropIncorrectItemIcon')
                        if (settings.highlightBucketOnDrop == true) {
                            $(this).addClass('dragDropIncorrectItem')
                        }
                        methods.checkAnswers()
                    }
                }
            })
        },

        checkAnswers: function() {
            var correct = false;
            var incorrect = false;
            var notMoved = 0;

            settings.elements.main.find('.dragDropItem').each(function(i) {

                if (settings.allCorrectMode) {
                    if ($(this).hasClass('dragDropCorrectItem') || $(this).hasClass('dragDropCorrectItemIcon')) {
                        correct = true;
                    } else {
                        correct = false;
                        return false;
                    }
                }
                else {
                    if ($(this).hasClass("not-moved")) {
                        notMoved++;
                    }
                }
            })

            if (settings.allCorrectMode == false && notMoved == 0) {
                settings.elements.main.find('.dragDropItem').each(function(i) {
                    if ($(this).hasClass('dragDropIncorrectItemIcon') || $(this).hasClass('dragDropIncorrectItem')) {
                        incorrect = true;
                        correct = false;
                        return false;
                    }
                    else if ($(this).hasClass('dragDropCorrectItemIcon') || $(this).hasClass('dragDropCorrectItem')) {
                        correct = true;
                        incorrect = false;
                    }
                })
            }

            settings.elements.scrRdr.html('You selected the correct bucket, Well Done! Keep going, there is still more items.')

            if (correct && notMoved == 0) {
                settings.elements.main.find('.dragDropBucket').removeAttr('tabindex')
                settings.elements.popUp.hide()
                settings.elements.wrap.append(settings.elements.popUp)
                settings.elements.popUp.fadeIn()
                settings.elements.main.find('.dragDropResetBtn').parent().prepend("<p>" + settings.feedback + "</p>")
                settings.elements.scrRdr.html('You got some answers incorrect, try again.')
                settings.elements.main.find('.dragDropPromptBox').focus()
                methods.bindEvents()
            }

            if (!settings.allCorrectMode && notMoved == 0 && incorrect) {
                settings.elements.main.find('.dragDropBucket').removeAttr('tabindex')
                settings.elements.popUp.hide()
                settings.elements.wrap.append(settings.elements.popUp)
                settings.elements.popUp.fadeIn()
                settings.elements.main.find('.dragDropResetBtn').parent().prepend("<p>" + settings.incorrectFeedback + "</p>")
                settings.elements.scrRdr.html('Congratulations, you have completed the activity, click the tab key to locate the reset button and then click the enter key to reset the activity.')
                settings.elements.main.find('.dragDropPromptBox').focus()
                methods.bindEvents()
            }
        },


        bindEvents : function() {
            settings.elements.main.find('.dragDropResetBtn').on('click', function() {
                methods.resetDragDrop()
            })
            $(window).resize(function() {
                if (settings.mode != 'freeform')
                    methods.setBucketDimensions()
            })

            settings.elements.main.find('.dragDropItem').hover(function() {
                $(this).toggleClass('draggableItemHover')
            }, function() {
                $(this).toggleClass('draggableItemHover')
            })
        },

        bindScrRdrEvents: function() {
            settings.elements.wrap.append(settings.elements.scrRdr)

            settings.elements.scrRdr.html('This activity requires you to listen to what each each item is and select the correct bucket for that item, to navigate you way through this activity use your tab keys to select each item, click the enter key to then choose a bucket to drop the item in, when you are happy with the bucket click the enter key once more and you will be informed whether you selected the correct or incorrect bucket, Have fun!')

            settings.elements.main.find('.dragDropItem').focus(function() {
                if ($(this).hasClass('dragDropIncorrectItem')) {
                    settings.elements.scrRdr.html("This item is incorrect, Click the enter key to select a bucket for, " + $(this).text())
                }
                else {
                    settings.elements.scrRdr.html("Click the enter key to select a bucket for, " + $(this).text())
                }
            })

            settings.elements.main.find('.dragDropBucket').focus(function() {
                var selected = settings.elements.main.find('.scrRdrSelected');
                settings.elements.scrRdr.html("Would you like to drop, " + selected.text() + ", into the, " + $(this).text() + " Bucket, Click the enter key to submit your answer.")
            })

            settings.elements.main.find('.dragDropItem').keydown(function(e) {
                if (e.which == 13) {
                    settings.elements.main.find('.dragDropItem').removeClass('scrRdrSelected')
                    $(this).addClass('scrRdrSelected')
                    settings.elements.main.find('.dragDropBucket')[0].focus()
                }
            })

            settings.elements.main.find('.dragDropBucket').keydown(function(e) {
                if (e.which == 13) {
                    var selected = settings.elements.main.find('.scrRdrSelected');
                    var itemBucketNum = settings.elements.main.find('.scrRdrSelected').data('bucket')
                    var bucketNum = $(this).data('bucket')
                    if (itemBucketNum == bucketNum) {
                        if (settings.backgroundFeedback == true)
                            selected.removeClass('dragDropIncorrectItem').addClass('dragDropCorrectItem')
                        if (settings.iconFeedback == true)
                            selected.removeClass('dragDropIncorrectItemIcon').addClass('dragDropCorrectItemIcon')
                        selected.removeAttr('tabindex')
                        selected.draggable('disable')
                        settings.elements.main.find('.dragDropInstructions').focus()
                        methods.checkAnswers()

                    }
                    else {
                        if (settings.backgroundFeedback == true)
                            selected.removeClass('dragDropCorrectItem').addClass('dragDropIncorrectItem')
                        if (settings.iconFeedback == true)
                            selected.removeClass('dragDropCorrectItemIcon').addClass('dragDropIncorrectItemIcon')
                        settings.elements.scrRdr.html('You selected the incorrect bucket for that item, keep trying.')
                        settings.elements.main.find('.dragDropInstructions').focus()
                        methods.checkAnswers()
                    }
                }
            })
        },

        setBucketDimensions: function() {
            settings.elements.main.find('.dragDropBucket').each(function() {
                $(this).css('width','100%').css('height','auto')
                $(this).css('width',$(this).width()).css('height',$(this).height())
            })
        },

        resetDragDrop : function() {
            settings.elements.wrap.fadeOut(function() {
                if (settings.enablePagination) {
                    settings.paginationPage = 1;
                    settings.paginationItems = [];
                    settings.dropCount = 0;
                }
                settings.elements.header.remove()
                settings.elements.wrap.remove()
                methods.init()
                settings.elements.main.find('.dragDropInstructions').focus()
            })
        },

        randomise: function(arr) {
            for(var j, x, i = arr.length; i; j = parseInt(Math.random() * i), x = arr[--i], arr[i] = arr[j], arr[j] = x);
            return arr;
        },

        shuffle: function(array) {
            var currentIndex = array.length, temporaryValue, randomIndex ;
            while (1 !== currentIndex) {
                randomIndex = Math.floor((Math.random() * currentIndex) + 1);;
                currentIndex -= 1;
                temporaryValue = array[currentIndex];
                array[currentIndex] = array[randomIndex];
                array[randomIndex] = temporaryValue;
            }
            return array;
        },

        paginationSetup: function() {
            settings.elements.wrap.children().each(function() {
                if ($(this).children().hasClass("dragDropItem")) {
                    settings.paginationItems.push($(this))
                }
                else if ($(this).hasClass("dragDropClear")) {
                    $(this).remove()
                }
                else if ($(this).children().hasClass("dragDropBucket")) {
                    $("<div class='dragDropClear'>").insertBefore($(this))
                    return false
                }
            })

            settings.elements.wrap.prepend("<p class='dragdrop-pagination-txt' style='padding: 15px; padding-bottom: 0px; margin-bottom: 0px'>Page "+settings.paginationPage+" of "+Math.ceil(settings.paginationItems.length / 4)+"</p>")

            $.each(settings.paginationItems, function(i) {
                $(this).removeAttr("class")
                $(this).addClass("col-xs-3 col-sm-3 col-md-3 dragDropVcenter")
                if (i >= 4) {
                    $(this).hide()
                }
            })
        },

        updatePagination: function() {
            if (settings.dropCount % 4 == 0) {
                settings.paginationPage++
                settings.elements.wrap.find(".dragdrop-pagination-txt").html("Page "+settings.paginationPage+" of "+Math.ceil(settings.paginationItems.length / 4))

                var count = 0;
                $.each(settings.paginationItems, function(i) {
                    if ($(this).hasClass("usedItem")) {

                    }
                    else {
                        if ($(this).css("display") != "none") {
                            $(this).hide()
                            $(this).addClass("usedItem")
                        }
                        else {
                            if (count < 4) {
                                $(this).show()
                                count++;
                            }
                        }
                    }
                })
            }
        }
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        methods.init(this)
    });
};
