jQuery.fn.checklist = function(options) {

    var defaults = {
        "col": "12",
        "title": "Checklist",
        "instructions": "Click on the checkboxes below to to show more information.",
        "insidePanel": false,
		"useHeadings": false
    };

    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            methods.createElements()
            methods.buildChecklist()
            methods.addText()
            methods.bindEvents()
            if (settings.usingStaticItems) {
                methods.setHeight();
                $(window).on("resize", function() {
                    methods.setHeight();
                })
            }
        },

        createElements : function() {

            settings.elements.titleWrapper = $('<div>')
            settings.elements.titleText = $('<i class="tqi tqi-activity tqi-2x tq-panel-icon pull-left"></i>' + '<h3 class="panel-title">'+settings.title+'</h3>')
            settings.elements.instructions = $('<p tabindex="0">'+settings.instructions+'</p>')
            settings.elements.checklistRow = $('<div class="checklist-main-wrapper">')
            settings.elements.showAllBtn = $('<div class="checklist-btn-clear checklist-btn-container col-md-6"><button aria-hidden="true" class="checklist-show-all-btn checklist-btn pull-right btn btn-success">Show All Items <i class="fa fa-plus-square"></i></button></div>')
            settings.elements.hideAllBtn = $('<div class="checklist-btn-container col-md-6"><button aria-hidden="true" class="checklist-hide-all-btn checklist-btn pull-right btn btn-danger">Hide All Items <i class="fa fa-minus-square"></i></button></div>')

            settings.elements.checkBox = $('<input tabindex="0" aria-labelledby="textAria_'+settings.elements.main.attr('id')+'" class="checkbox" type="checkbox">')
			
            settings.elements.checklistText = $('<p class="checklistText">')
            settings.elements.ariaTextBox = $('<div class="sr-only" id="textAria_'+settings.elements.main.attr('id')+'">')

            if (settings.insidePanel == true) {
                settings.elements.outerWrapper = $('<div class="checklistWrapper checklistWrapperInsidePanel input-group">')
                settings.elements.mainWrapper = $('<div class="mainChecklistWrapperInsidePanel mainChecklistWrapper col-md-'+settings.col+'">')
                settings.elements.main.parent().addClass('checklistPanel')
                settings.elements.titleBG = $('<div class="checklistTitleBG checklistTitleBGInsidePanel">')
                settings.elements.textWrapper = $('<div tabindex="0" class="textWrapper form-control textWrapperInsidePanel">')
                settings.elements.innerWrapper = $('<span class="input-group-addon input-group-addonInsidePanel">')
            }
            else {
                settings.elements.outerWrapper = $('<div class="checklistWrapper input-group">')
                settings.elements.mainWrapper = $('<div class="mainChecklistWrapper col-md-'+settings.col+'">')
                settings.elements.titleBG = $('<div class="checklistTitleBG">')
                settings.elements.textWrapper = $('<div tabindex="0" class="textWrapper form-control">')
                settings.elements.innerWrapper = $('<span class="input-group-addon">')
            }

            if (settings.usingStaticItems)
                settings.elements.staticText = $('<div tabindex="0" class="checklist-static-text col-sm-'+settings.staticCol+' col-md-'+settings.staticCol+'"><p></p></div>')
			if (settings.useHeadings) 
				settings.elements.checklistHeaderText = $('<h4 class="checklistHeaderText">')
        },

        buildChecklist : function() {
            settings.elements.main.append(settings.elements.ariaTextBox)
            settings.elements.main.append(settings.elements.titleWrapper)
            settings.elements.main.append(settings.elements.checklistRow)
            settings.elements.titleWrapper.append(settings.elements.titleBG)
            settings.elements.titleBG.append(settings.elements.titleText)
            settings.elements.titleBG.append(settings.elements.instructions)
            for (var i = 0; i < settings.container[0].list.length; i++) {
                if (settings.usingStaticItems)
                    var staticText = settings.elements.staticText.clone();
                var mainWrapper = settings.elements.mainWrapper.clone()
                var outter = settings.elements.outerWrapper.clone()
                var inner = settings.elements.innerWrapper.clone()
                var cBox = settings.elements.checkBox.clone()
                var tWrap = settings.elements.textWrapper.clone()
				if (settings.useHeadings) {var hText = settings.elements.checklistHeaderText.clone()}
                var text = settings.elements.checklistText.clone()

                if (settings.usingStaticItems)
                    settings.elements.checklistRow.append(staticText).append(mainWrapper)
                else
                    settings.elements.checklistRow.append(mainWrapper)

                mainWrapper.append(outter)

                outter.append(inner)
                inner.append(cBox.attr('data-num', i))
                outter.append(tWrap)
				
				if (settings.useHeadings)
                    tWrap.append(hText).append(text.attr('data-num', i))
                else
                    tWrap.append(text.attr('data-num', i))
				 
            }
            settings.elements.checklistRow.append(settings.elements.showAllBtn)
            settings.elements.checklistRow.append(settings.elements.hideAllBtn)
            settings.elements.checklistRow.append($("<div class='clearfix'>"))
        },

        addText : function() {
			settings.elements.main.find(".checklistHeaderText").each(function(i) {
				if (settings.useHeadings) {
				if (settings.container[0].list[i].heading) {
					$(this).html(settings.container[0].list[i].heading)}
				}
            })
            settings.elements.main.find(".checklistText").each(function(i) {
                $(this).html(settings.container[0].list[i].item).addClass('hiddenChecklistText')
            })
            settings.elements.main.find(".checklist-static-text").each(function(i) {
                $(this).children().html(settings.container[0].list[i].staticItem)
            })
        },

        bindEvents : function() {
            settings.elements.main.find(".input-group-addon").on("click", function() {
                methods.toggleActive($(this).parent())
                methods.toggleText($(this).find(".checkbox"))

            })

            settings.elements.main.find(".input-group-addon").parent().focus(function() {
                methods.changeScrRdrTextBox($(this).find(".checkbox").children())
            })

            settings.elements.main.find(".checklist-show-all-btn").on("click", function() {
                methods.showAllItems()
            });

            settings.elements.main.find(".checklist-hide-all-btn").on("click", function() {
                methods.hideAllItems()
            });
        },

        showAllItems: function() {
            settings.elements.main.find(".checklistWrapper").each(function(i) {
                $(this).find(".checkbox").parent().addClass('checkboxHighlight')
                $(this).find(".checklistText").removeClass('hiddenChecklistText')
                $(this).find(".checklistText").show()
                $(this).find('input[type="checkbox"]').prop('checked', true);
            })
        },

        hideAllItems: function() {
            settings.elements.main.find(".checklistWrapper").each(function(i) {
                $(this).find(".checkbox").parent().removeClass('checkboxHighlight');
                $(this).find(".checklistText").addClass('hiddenChecklistText')
                $(this).find(".checklistText").hide()
                $(this).find('input[type="checkbox"]').prop('checked', false);
            })
        },

        toggleActive : function(btn) {
            if (btn.find(".checklistText").hasClass("hiddenChecklistText")) {
                btn.find(".checkbox").parent().addClass('checkboxHighlight');
                btn.find('input[type="checkbox"]').prop('checked', true);
            }
            else {
                btn.find(".checkbox").parent().removeClass('checkboxHighlight');
                btn.find('input[type="checkbox"]').prop('checked', false);
            }
        },

        toggleText : function(btn) {
            var current = btn.data('num')
            settings.elements.main.find(".checklistText").each(function(i) {
                if ($(this).data('num') == current) {
                    if ($(this).hasClass('hiddenChecklistText')) {
                        $(this).hide()
                        $(this).toggleClass('hiddenChecklistText')
                        $(this).fadeIn()
                    }
                    else {
                        $(this).fadeOut(function(){
                            $(this).toggleClass('hiddenChecklistText')
                        })
                    }
                }
            });
            btn.parent().parent().find('.textWrapper').focus()
        },

        changeScrRdrTextBox : function(btn) {
            var current = btn.data('num')
            settings.elements.main.find(".checklistText").each(function(i) {
                if ($(this).data('num') == current) {
                    settings.elements.ariaTextBox.html($(this).text())
                }
            });
        },

        setHeight: function() {
            settings.elements.main.find(".textWrapper").each(function(i) {
                settings.elements.main.find(".checklist-static-text").eq(i).css("height", 'auto');
                $(this).css("height", 'auto');
                $(this).parent().css("height", 'auto');

                var largest = 0;

                var staticHeight = Number($(this).css("height").split("px")[0]);
                var textHeight = Number(settings.elements.main.find(".checklist-static-text").eq(i).css("height").split("px")[0]);

                if (staticHeight >= textHeight && staticHeight >= largest)
                    largest = staticHeight;
                else if (textHeight >= staticHeight && textHeight >= largest)
                    largest = textHeight;
                settings.elements.main.find(".checklist-static-text").eq(i).css("height", largest);
                $(this).css("height", largest);
                $(this).parent().css("height", largest);
            })

        },
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        if (settings.insidePanel == true)
            settings.elements.main.addClass('row checklistContainer')
        else
            settings.elements.main.addClass('checklistContainer')
        methods.init(this)
    });
};
