jQuery.fn.flipcard = function(options) {

    var defaults = {
        dropShadow: true,
        isIE: false,
        perspective: 1000,
        duration: '0.6s',
        trigger: 'hover',
        animationStyle: 'linear',
        cols: 3,
        instructions: "Click the buttons to learn more",
        fullWidthCards: false
    }

    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            methods.detectIE()
            methods.createElements()
            methods.build()
            methods.applyCSS()
            methods.loadImages()
            methods.addContent()
            methods.bindCardFlipEvents()
            methods.screenReader()
            methods.wrapText()
            methods.resizeCards()
            methods.addIcon()
        },

        createElements: function() {
            settings.elements.scrRdr = $("<div aria-live='assertive' id='screenRdr_"+settings.elements.main.attr('id')+"' class='sr-only'>")
            settings.elements.colContainer = $("<div class='flipcardColContainer'>")
            settings.elements.flipIcon = $("<i class='flipcard-flip-icon fa fa-reply-all fa-rotate-180'>")
            settings.elements.innerContainer = $("<div class='flipcardInnerContainer'>")
            settings.elements.button = $("<button aria-labelledby='screenRdr_"+settings.elements.main.attr('id')+"' class='btn btn-default flipcardBtn'>")
            if (settings.isIE == false) {
                settings.elements.outerContainer = $("<div aria-labelledby='screenRdr_"+settings.elements.main.attr('id')+"' href='javascript:void(0)' tabindex='0' class='flipcardOuterContainer'>")
                settings.elements.cardFront = $("<div class='flipActive flipcard flipcardFront'>")
                settings.elements.cardBack = $("<div class='flipcardBack flipcard'>")
            } else {
                settings.elements.outerContainer = $("<div aria-labelledby='screenRdr_"+settings.elements.main.attr('id')+"' href='javascript:void(0)' tabindex='0' class='flipcardOuterContainer flipcardOuterContainerIE'>")
                settings.elements.cardFront = $("<div class='flipActive flipcardFront flipcardFrontIE'>")
                settings.elements.cardBack = $("<div class='flipcardBack flipcardBackIE'>")
            }
        },

        build: function(boo) {
            settings.elements.scrRdr.insertBefore(settings.elements.main)

            for (var i = 0; i < settings.cards.length; i++) {
                settings.elements.main
                .append(settings.elements.colContainer.clone()
                    .append(settings.elements.outerContainer.clone()
                    .append(settings.elements.innerContainer.clone()))
                .addClass('col-md-'+settings.cols+' col-sm-'+settings.cols+' col-xs-6'))
            }

            settings.elements.main.find('.flipcardInnerContainer').each(function(i) {
                $(this).append(settings.elements.cardFront.clone()).append(settings.elements.cardBack.clone())
            });
        },

        applyCSS: function() {
            settings.elements.main.find('.flipcardOuterContainer').each(function(i) {
                if (settings.dropShadow) {
                    $(this).find('.flipcardBack').addClass("flipcard-dropshadow")
                    $(this).find('.flipcardFront').addClass("flipcard-dropshadow")
                }
            })

            // Applying styles for browser support.
            if (settings.isIE == false) {
                settings.elements.main.find('.flipcardOuterContainer')
                .css('perspective', settings.perspective)
                .css('-webkit-perspective', settings.perspective)
                .css('-o-perspective', settings.perspective)
                .css('-moz-perspective', settings.perspective)
                .css('-ms-perspective', settings.perspective)

                settings.elements.main.find('.flipcard')
                .css('transition', settings.duration + ' ' + settings.animationStyle)
                .css('-webkit-transition', settings.duration + ' ' + settings.animationStyle)
                .css('-o-transition', settings.duration + ' ' + settings.animationStyle)
                .css('-moz-transition', settings.duration + ' ' + settings.animationStyle)
                .css('-ms-transition', settings.duration + ' ' + settings.animationStyle)
            }
            else {
                settings.elements.main.find('.flipcardBack').each(function(i) {
                    $(this).css("transform","rotateY(0deg)")
                })
            }
        },

        loadImages : function() {
            for (var i = 0; i < settings.cards.length; i++) {
                var fdata = settings.cards[i].front;
                var bdata = settings.cards[i].back;
                var src = "";
                if (fdata.toString().indexOf("img") > -1) {
                    if (fdata.indexOf(".jpg") < 0) {
                        var src = fdata.substring(fdata.indexOf("src=")+5,fdata.indexOf(".png")+4)
                    }
                    else {
                        var src = fdata.substring(fdata.indexOf("src=")+5,fdata.indexOf(".jpg")+4)
                    }
                }
                else if (bdata.toString().indexOf("img") > -1) {
                    if (bdata.indexOf(".jpg") < 0) {
                        var src = bdata.substring(bdata.indexOf("src=")+5,bdata.indexOf(".png")+4)
                    }
                    else {
                        var src = bdata.substring(bdata.indexOf("src=")+5,bdata.indexOf(".jpg")+4)
                    }
                }



                var img = new Image();

                img.onload = function() {
                    methods.resizeCards()
                }

                img.src = src;
            }
        },

        addContent: function(boo) {
            settings.elements.main.find('.flipcardFront').each(function(i) {
                $(this).html(settings.cards[i].front)
                $(this).children().load(function() {
                    methods.applyCSS()
                })
                if ($(this).children().has("img")) {
                    $(this).children().addClass('img-responsive')
                }

                var str1 = "" + settings.cards[i].front + ""
                var str2 = "" + settings.cards[i].back + ""

                if (str1.indexOf('<p>') > -1) {
                    $(this).addClass('flipcardTextCard')
                }
            });

            settings.elements.main.find('.flipcardBack').each(function(i) {
                $(this).html(settings.cards[i].back)
                var str = "" + settings.cards[i].back + ""
                if (str.indexOf('<p>') > -1) {
                    $(this).addClass('flipcardTextCard')
                }
            });

            if (settings.fullWidthCards) {
                settings.elements.main.find('.flipcardColContainer')
                    .removeClass("col-md-3")
                    .addClass("col-md-12")
            }
            else {
                methods.offsetCols()
            }
        },

        bindCardFlipEvents: function() {
            if (settings.isIE == false) {
                settings.elements.main.find(".flipcardOuterContainer").keydown(function(e) {
                    if (e.which == 13) {
                        if (settings.trigger == 'click') {
                            $(this).children().toggleClass('flipThis')
                        }
                        else if (settings.trigger == 'hover') {
                            settings.elements.main.find('.flipcardInnerContainer').removeClass('flipThis')
                            $(this).children().addClass('flipThis')
                        }
                        $(this).find('.flipcardFront').toggleClass('flipActive')
                        $(this).find('.flipcardBack').toggleClass('flipActive')
                    }
                });
                if (settings.trigger == 'click') {
                    settings.elements.main.find(".flipcardOuterContainer").click(function() {
                        $(this).children().toggleClass('flipThis')
                        $(this).find('.flipcardFront').toggleClass('flipActive')
                        $(this).find('.flipcardBack').toggleClass('flipActive')
                    })
                }
                else if (settings.trigger == 'hover') {
                    settings.elements.main.find(".flipcardOuterContainer").hover(function() {
                        $(this).children().toggleClass('flipThis')
                        $(this).find('.flipcardFront').toggleClass('flipActive')
                        $(this).find('.flipcardBack').toggleClass('flipActive')
                    }, function() {
                        $(this).children().toggleClass('flipThis')
                        $(this).find('.flipcardFront').toggleClass('flipActive')
                        $(this).find('.flipcardBack').toggleClass('flipActive')
                    })
                }
            }
            // IE Fallback
            else {
                $('.flipcardBack').hide()
                settings.elements.main.find(".flipcardOuterContainer").keydown(function(e) {
                    if (e.which == 13) {
                        $(this).find('.flipcardFront').fadeToggle()
                        $(this).find('.flipcardBack').fadeToggle()
                    }
                });
                settings.elements.main.find(".flipcardOuterContainer").click(function() {
                    $(this).find('.flipcardFront').fadeToggle()
                    $(this).find('.flipcardBack').fadeToggle()
                })
            }

            $(window).resize(function() {
                methods.resizeCards()
            })
        },

        detectIE: function() {
            if (navigator.userAgent.indexOf('MSIE 10.0') > -1) {
                settings.isIE = true;
            }
            else if (navigator.userAgent.indexOf('MSIE') !== -1) {
                settings.isIE = true;
            }
            else {
                settings.isIE = false;
            }
        },

        screenReader: function() {
            settings.elements.main.find('.flipcardOuterContainer').keydown(function(e) {
                if (e.which == 13) {
                    settings.elements.scrRdr.html($(this).find('.flipActive').html())
                }
            })
            settings.elements.main.find('.flipcardOuterContainer').focus(function() {
                settings.elements.scrRdr.html($(this).find('.flipActive').html())
            })
        },

        offsetCols : function() {
            var cards = settings.elements.main.find('.flipcardColContainer');
            var length = cards.length;
            var colNum = 3;
            var colOffset = 0;

            function changeClass(ctx) {
                if (colNum == 12) {
                    ctx.removeAttr("class").addClass("flipcardColContainer").addClass("col-xs-12").addClass("col-sm-"+colNum+"").addClass("col-md-"+colNum+"")
                    ctx.addClass("col-xs-offset-0").addClass("col-sm-offset-"+colOffset+"").addClass("col-md-offset-"+colOffset+"")
                }
                else {
                    ctx.removeAttr("class").addClass("flipcardColContainer").addClass("col-xs-6").addClass("col-sm-"+colNum+"").addClass("col-md-"+colNum+"")
                    ctx.addClass("col-xs-offset-0").addClass("col-sm-offset-"+colOffset+"").addClass("col-md-offset-"+colOffset+"")
                }
            }

            if (length > 4) {
                if (length == 5) {
                    var select = (length % 4) + 5;
                    var ctx = cards;
                    colNum = 4;
                    changeClass(ctx)
                    colOffset = 2;
                    select = (length % 4) + 1;
                    ctx = cards.eq(length - select);
                    changeClass(ctx)
                }
                else if (length % 4 == 3) {
                    var select = (length % 4) + 1;
                    var ctx = cards.eq(length - select).nextAll();
                    colNum = 4;
                    changeClass(ctx)
                }
                else if (length % 4 == 2) {
                    var select = (length % 4);
                    var ctx = cards.eq(length - select);
                    colOffset = 3;
                    changeClass(ctx)
                }
                else if (length % 4 == 1) {
                    var select = (length % 4) + 5;
                    var ctx = cards.eq(length - select).nextAll();
                    colNum = 4;
                    changeClass(ctx)
                    colOffset = 2;
                    select = (length % 4) + 1;
                    ctx = cards.eq(length - select);
                    changeClass(ctx)
                }
            }
            else {
                if (length % 4 == 3) {
                    var ctx = cards;
                    colNum = 4;
                    changeClass(ctx)
                }
                else if (length % 4 == 2) {
                    var ctx = cards;
                    colNum = 6;
                    changeClass(ctx)
                }
                else if (length % 4 == 1) {
                    var ctx = cards;
                    colNum = 12;
                    changeClass(ctx)
                }
            }
        },

        wrapText : function() {
            settings.elements.main.find('.flipcardOuterContainer').each(function() {
                $(this).children().children().wrapInner($("<span class='flipcardSpan'></span>"))
            })
        },

        resizeCards : function() {
            settings.elements.main.find('.flipcardOuterContainer').css("height", "auto")
            settings.elements.main.find('.flipcardOuterContainer').find(".flipcard").css("height", "auto")
            settings.elements.main.find('.flipcardOuterContainer').each(function(i) {
                var height = 0;
                if ($(this).find('.flipcardFront').height() > $(this).find('.flipcardBack').height()) {
                    height = $(this).find('.flipcardFront').height();
                }
                else {
                    height = $(this).find('.flipcardBack').height();
                }
                $(this).find('.flipcard').css('height', height)
                $(this).css('height', height)
            })
        },

        addIcon: function() {
            settings.elements.main.find('.flipcardFront').append(settings.elements.flipIcon.clone())
            settings.elements.main.find('.flipcardBack').append(settings.elements.flipIcon.clone())
        }
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this).addClass("row")
        methods.init(this)
    });
};
