jQuery.fn.flipcarousel = function(options) {

    var defaults = {
        usingAudio: false,
        autoPlayAudio: false,
        flippable: false,
        textAlign: "center",
        nextBtnTxt: "Next",
        nextBtnIcon: "fa-chevron-circle-right",
        prevBtnTxt: "Prev",
        prevBtnIcon: "fa-chevron-circle-left",
        flipBtnTxt: "Click this button to flip the card over",
        flipBtnIcon: "fa-refresh",
        transBtnTxt: "Transcript",
        transBtnIcon: "fa-file-text"
    }

    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            methods.detectIE();
            methods.createElements();
            methods.createBreadcrumbContainers();
            methods.createCrumbs();
            methods.updateCrumb();
            methods.bindCrumbClick();
            methods.setCounterText();
            methods.createCard();
            methods.createUi();
            methods.createScreenReader();
            methods.addContent();
            methods.setHeight();
            if (settings.flippable) {
                methods.bindFlip();
            }
            methods.bindNext();
            methods.bindPrev();
            if (settings.usingAudio) {
                methods.changeAudioAndTranscript();
            }
            methods.bindScreenReaderEvents();
            // $(window).resize(function() {
            //     methods.setHeight();
            // })
        },

        detectIE: function() {
            // if (navigator.userAgent.indexOf('MSIE 10.0') > -1) {
            //     return(false);
            // }
            // else if (navigator.userAgent.indexOf('MSIE') !== -1) {
            //     return(true);
            // }
            // else {
            //     return(false);
            // }
            return(true);
        },

        createElements: function() {
            settings.elements.count = 1;
            settings.elements.breadcrumbWrapper = $('<div class="row">');
            settings.elements.breadcrumbContainer = $('<div class="col-md-10 hidden-xs flipcarousel-breadcrumbs">');
            settings.elements.breadcrumbCrumb = $('<i aria-labelledby="flipcarousel-screen-reader-' + settings.elements.main.attr('id') + '" tabindex="0" class="flipcarousel-crumb fa fa-circle"></i>');
            settings.elements.breadcrumbCounter = $('<div class="col-md-2 flipcarousel-counter">');
            settings.elements.breadcrumbCounterText = $('<p tabindex="0" class="text-right"></p>');

            settings.elements.container = $('<div aria-labelledby="flipcarousel-screen-reader-' + settings.elements.main.attr('id') + '" tabindex="0" class="flipcarousel">');
            if (methods.detectIE())
                settings.elements.front = $('<div class="flipcarousel-card flipcarousel-front flipcarousel-front-ie">');
            else
                settings.elements.front = $('<div class="flipcarousel-card flipcarousel-front">');
            settings.elements.back = $('<div class="flipcarousel-card flipcarousel-back">');

            settings.elements.uiContainer = $('<div class="row flipcarousel-ui-container">');

            if (settings.flippable && settings.usingAudio) {
                settings.elements.prev = $('<div class="col-md-3 col-sm-3 flipcarousel-prev"><a tabindex="0" class="btn btn-success flipcarousel-btn"><i class="fa '+settings.prevBtnIcon+'"></i> '+settings.prevBtnTxt+'</a></div>');
                settings.elements.next = $('<div class="col-md-3 col-sm-3 flipcarousel-next"><a tabindex="0" class="btn btn-success flipcarousel-btn">'+settings.nextBtnTxt+' <i class="fa '+settings.nextBtnIcon+'"></i></a></div>');
                settings.elements.flip = $('<div class="col-md-6 col-sm-6 flipcarousel-flip"><a tabindex="0" class="btn btn-default flipcarousel-btn"><i class="fa '+settings.flipBtnIcon+'"></i> '+settings.flipBtnTxt+'</a></div>');
                if (settings.autoPlayAudio) {
                    settings.elements.audio = $('<div class="tq-media-player col-md-9 col-sm-9"><audio preload="true" autoplay="true" type="audio/mp3" src="" controls="controls"></audio></div>');
                }
                else {
                    settings.elements.audio = $('<div class="tq-media-player col-md-9 col-sm-9"><audio preload="true" type="audio/mp3" src="" controls="controls"></audio></div>');
                }
                settings.elements.transcriptBtn = $('<div aria-hidden="true" class="col-md-3 col-sm-3 flipcarousel-transcript-btn"><a tabindex="-1" class="btn btn-default flipcarousel-btn"><i class="fa '+settings.transBtnIcon+'"></i> '+settings.transBtnTxt+'</a></div>')
            }
            else if (!settings.flippable && settings.usingAudio) {
                settings.elements.prev = $('<div class="col-md-6 col-sm-6 flipcarousel-prev"><a tabindex="0" class="btn btn-success flipcarousel-btn"><i class="fa '+settings.prevBtnIcon+'"></i> '+settings.prevBtnTxt+'</a></div>');
                settings.elements.next = $('<div class="col-md-6 col-sm-6 flipcarousel-next"><a tabindex="0" class="btn btn-success flipcarousel-btn">'+settings.nextBtnTxt+' <i class="fa '+settings.nextBtnIcon+'"></i></a></div>');
                if (settings.autoPlayAudio) {
                    settings.elements.audio = $('<div class="tq-media-player col-md-6 col-sm-6"><audio preload="true" autoplay="true" type="audio/mp3" src="" controls="controls"></audio></div>');
                }
                else {
                    settings.elements.audio = $('<div class="tq-media-player col-md-6 col-sm-6"><audio preload="true" type="audio/mp3" src="" controls="controls"></audio></div>');
                }
                settings.elements.transcriptBtn = $('<div aria-hidden="true" class="col-md-6 col-sm-6 flipcarousel-transcript-btn"><a tabindex="-1" class="btn btn-default flipcarousel-btn"><i class="fa '+settings.transBtnIcon+'"></i> '+settings.transBtnTxt+'</a></div>')
            }
            else if (settings.flippable && !settings.usingAudio) {
                settings.elements.prev = $('<div class="col-md-2 col-sm-2 flipcarousel-prev"><a tabindex="0" class="btn btn-success flipcarousel-btn"><i class="fa '+settings.prevBtnIcon+'"></i> '+settings.prevBtnTxt+'</a></div>');
                settings.elements.next = $('<div class="col-md-2 col-sm-2 flipcarousel-next"><a tabindex="0" class="btn btn-success flipcarousel-btn">'+settings.nextBtnTxt+' <i class="fa '+settings.nextBtnIcon+'"></i></a></div>');
                settings.elements.flip = $('<div class="col-md-8 col-sm-8 flipcarousel-flip"><a tabindex="0" class="btn btn-default flipcarousel-btn"><i class="fa '+settings.flipBtnIcon+'"></i> '+settings.flipBtnTxt+'</a></div>');
            }
            else if (!settings.flippable && !settings.usingAudio) {
                settings.elements.prev = $('<div class="col-md-2 col-sm-2 flipcarousel-prev"><a tabindex="0" class="btn btn-success flipcarousel-btn"><i class="fa '+settings.prevBtnIcon+'"></i> '+settings.prevBtnTxt+'</a></div>');
                settings.elements.next = $('<div class="col-md-2 col-md-offset-8 col-sm-offset-8 col-sm-2 flipcarousel-next"><a tabindex="0" class="btn btn-success flipcarousel-btn">'+settings.nextBtnTxt+' <i class="fa '+settings.nextBtnIcon+'"></i></a></div>');
            }

            settings.elements.transcriptText = $('<div class="col-md-12 flipcarousel-transcript-text"><div></div></div>').hide();

            settings.elements.screenReader = $('<div class="sr-only" id="flipcarousel-screen-reader-' + settings.elements.main.attr('id') + '">')
        },

        createBreadcrumbContainers: function() {
            settings.elements.main.append(settings.elements.breadcrumbWrapper);
            settings.elements.breadcrumbWrapper.append(settings.elements.breadcrumbContainer);
            settings.elements.breadcrumbWrapper.append(settings.elements.breadcrumbCounter);
            settings.elements.breadcrumbCounter.append(settings.elements.breadcrumbCounterText);
        },

        createCrumbs: function() {
            for (var i = 0; i < settings.card.length; i++) {
                settings.elements.breadcrumbContainer.append(settings.elements.breadcrumbCrumb.clone().attr("data-crumb",i+1));
            }
        },

        updateCrumb: function() {
            settings.elements.main.find(".flipcarousel-crumb").removeClass("fa-dot-circle-o").addClass("fa-circle");
            settings.elements.main.find(".flipcarousel-crumb").eq(settings.elements.count - 1).removeClass("fa-circle").addClass("fa-dot-circle-o");
        },

        bindCrumbClick: function() {
            bind();
            function bind() {
                settings.elements.main.find(".flipcarousel-breadcrumbs").children().bind("click", function() {
                    settings.elements.main.find(".flipcarousel-breadcrumbs").children().unbind("click");
                    settings.elements.main.find(".flipcarousel-breadcrumbs").children().unbind("keydown");
                    settings.elements.count = $(this).data("crumb")
                    methods.setCounterText();
                    methods.updateCrumb();
                    methods.flipAndChange("thumb");
                })

                settings.elements.main.find(".flipcarousel-breadcrumbs").children().bind("keydown", function(e) {
                    if (e.which == 13) {
                        settings.elements.main.find(".flipcarousel-breadcrumbs").children().unbind("click");
                        settings.elements.main.find(".flipcarousel-breadcrumbs").children().unbind("keydown");
                        settings.elements.count = $(this).data("crumb")
                        methods.setCounterText();
                        methods.updateCrumb();
                        methods.flipAndChange("thumb");
                    }
                });
            }
        },

        setCounterText: function() {
            settings.elements.breadcrumbCounterText.html("Card "+settings.elements.count+" of "+settings.card.length+"");
        },

        createCard: function() {
            settings.elements.main.append(settings.elements.container);
            settings.elements.container.append(settings.elements.front);
            settings.elements.container.append(settings.elements.back);

            if (methods.detectIE()) {
                settings.elements.back.hide()
            }

            settings.elements.front.css("textAlign", settings.textAlign)
            settings.elements.back.css("textAlign", settings.textAlign)
        },

        createUi: function() {
            settings.elements.main.append(settings.elements.uiContainer);

            if (settings.flippable && settings.usingAudio) {
                settings.elements.uiContainer.append(settings.elements.audio);
                settings.elements.uiContainer.append(settings.elements.transcriptBtn)
                settings.elements.uiContainer.append(settings.elements.prev);
                settings.elements.uiContainer.append(settings.elements.flip);
                settings.elements.uiContainer.append(settings.elements.next);
                settings.elements.main.append(settings.elements.transcriptText)
            }
            else if (!settings.flippable && settings.usingAudio) {
                settings.elements.uiContainer.append(settings.elements.audio);
                settings.elements.uiContainer.append(settings.elements.transcriptBtn)
                settings.elements.uiContainer.append(settings.elements.prev);
                settings.elements.uiContainer.append(settings.elements.next);
                settings.elements.main.append(settings.elements.transcriptText)
            }
            else if (settings.flippable && !settings.usingAudio) {
                settings.elements.uiContainer.append(settings.elements.prev);
                settings.elements.uiContainer.append(settings.elements.flip);
                settings.elements.uiContainer.append(settings.elements.next);
            }
            else if (!settings.flippable && !settings.usingAudio) {
                settings.elements.uiContainer.append(settings.elements.prev);
                settings.elements.uiContainer.append(settings.elements.next);
            }
        },

        createScreenReader: function() {
            settings.elements.main.append(settings.elements.screenReader);
        },

        changeAudioAndTranscript: function() {

            if (settings.elements.main.find(".flipcarousel").hasClass("flipped")) {
                if (settings.card[settings.elements.count - 1].back.audio == undefined) {
                    hide();
                }
                else {
                    show("back");
                }
            }
            else {
                if (settings.card[settings.elements.count - 1].front.audio == undefined) {
                    hide();
                }
                else {
                    show("front");
                }
            }

            function hide() {
                $(settings.elements.main.find(".tq-media-player")[0]).hide();
                $(settings.elements.main.find(".flipcarousel-transcript-btn")[0]).hide();
                $(settings.elements.main.find(".flipcarousel-transcript-text")[0]).fadeOut();
            }

            function show(side) {
                $(settings.elements.main.find(".tq-media-player")[0]).show();
                $(settings.elements.main.find(".flipcarousel-transcript-btn")[0]).show();
                $(settings.elements.main.find(".flipcarousel-transcript-text")[0]).fadeOut();

                settings.elements.main.find(".mejs-pause").removeClass("mejs-pause").addClass("mejs-play")

                if (side == "front") {
                    settings.elements.main.find("audio")[0].attributes.src.nodeValue = settings.card[settings.elements.count - 1].front.audio.src;
                    settings.elements.transcriptText.children().html(
                        "<h4>Audio Transcript:</h4>"
                        + settings.card[settings.elements.count - 1].front.audio.transcript);
                }
                else {
                    settings.elements.main.find("audio")[0].attributes.src.nodeValue = settings.card[settings.elements.count - 1].back.audio.src;
                    settings.elements.transcriptText.children().html(
                        "<h4>Audio Transcript:</h4>"
                        + settings.card[settings.elements.count - 1].back.audio.transcript);
                }

                if (settings.usingAudio) {
                    methods.bindTranscriptBtn();
                }
                settings.elements.main.find("audio").mediaelementplayer({
                    alwaysShowControls: true,
                });
            }
        },

        bindTranscriptBtn: function() {
            settings.elements.main.find(".flipcarousel-transcript-btn").unbind("click")
            bind();

            function bind() {
                settings.elements.main.find(".flipcarousel-transcript-btn").bind("click", function() {
                    $(this).children().addClass("active");
                    $(this).unbind("click");
                    $(this).unbind("keydown");
                    toggle(settings.elements.main.find(".flipcarousel-transcript-text"));
                })

                settings.elements.main.find(".flipcarousel-transcript-btn").bind("keydown", function(e) {
                    if (e.which == 13) {
                        $(this).children().addClass("active");
                        $(this).unbind("click");
                        $(this).unbind("keydown");
                        toggle(settings.elements.main.find(".flipcarousel-transcript-text"));
                    }
                });
            }

            function toggle(ctx) {
                if (ctx.hasClass("visible")) {
                    settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                    ctx.removeClass("visible")
                    ctx.hide();
                    bind();
                }
                else {
                    ctx.addClass("visible")
                    ctx.show();
                    bind();
                }
            }
        },

        addContent: function() {
            settings.elements.front.html(settings.card[0].front.html);
            if (settings.flippable)
                settings.elements.back.html(settings.card[0].back.html);
        },

        setHeight: function() {
            // settings.elements.container.css("height", "auto")
            // settings.elements.front.css("height", "auto")
            // settings.elements.back.css("height", "auto")

            // var $images = $('div.flipcarousel-card img').clone();
            // var loaded_images_total = 0;
            // var check = function() {
            //     var front = settings.elements.front.css("height").split("px")[0]
            //     var back = settings.elements.back.css("height").split("px")[0]
            //     if (parseInt(front) > parseInt(back)) {
            //         settings.elements.container.css("height", front)
            //         settings.elements.front.css("height", front)
            //         settings.elements.back.css("height", front)
            //     }
            //     else {
            //         settings.elements.container.css("height", back)
            //         settings.elements.front.css("height", back)
            //         settings.elements.back.css("height", back)
            //     }
            // }

            // if ($images.length == 0) {
            //     check()
            // }
            // else {

            //     $images.load(function(){
            //         loaded_images_total ++;

            //         if (loaded_images_total == $images.length) {
            //             check()
            //         }
            //     });
            // }
        },

        bindFlip: function() {
            if (methods.detectIE() == false) {
                function bind() {
                    settings.elements.flip.bind("click", function() {
                        $(this).unbind("click");
                        $(this).unbind("keydown");
                        flip();
                    });

                    settings.elements.flip.bind("keydown", function(e) {
                        if (e.which == 13) {
                            $(this).unbind("keydown");
                            $(this).unbind("click");
                            flip();
                        }
                    });
                };

                function flip() {
                    if (settings.elements.main.find(".flipcarousel").hasClass("flipped")) {
                        settings.elements.main.find(".flipcarousel").children().first().transition({
                            perspective: '3000',
                            rotateX: '360deg',
                        });
                        settings.elements.main.find(".flipcarousel").children().last().transition({
                            perspective: '3000',
                            rotateX: '540deg',
                            complete: function() {
                                bind();
                                settings.elements.main.find(".flipcarousel").removeClass("flipped")
                                if (settings.usingAudio) {
                                    methods.changeAudioAndTranscript();
                                    settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                                    settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                                    settings.elements.main.find(".flipcarousel").focus()
                                }
                            }
                        });
                    }
                    else {
                        settings.elements.main.find(".flipcarousel").children().first().transition({
                            perspective: '3000',
                            rotateX: '180deg',
                        });
                        settings.elements.main.find(".flipcarousel").children().last().transition({
                            perspective: '3000',
                            rotateX: '360deg',
                            complete: function() {
                                bind();
                                settings.elements.main.find(".flipcarousel").addClass("flipped")
                                if (settings.usingAudio) {
                                    methods.changeAudioAndTranscript();
                                    settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                                    settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                                    settings.elements.main.find(".flipcarousel").focus()
                                }
                            }
                        });
                    }
                };
                bind();
            }
            else {
                bindIE();
                settings.elements.back.hide();

                function bindIE() {
                    settings.elements.flip.bind("click", function() {
                        $(this).unbind("click");
                        $(this).unbind("keydown");
                        if (settings.elements.main.find(".flipcarousel").hasClass("flipped")) {
                            fadeFront(settings.elements.main.find(".flipcarousel"));
                        }
                        else {
                            fadeBack(settings.elements.main.find(".flipcarousel"));
                        }
                    })

                    settings.elements.flip.bind("keydown", function(e) {
                        if (e.which == 13) {
                            $(this).unbind("keydown");
                            $(this).unbind("click");
                            if (settings.elements.main.find(".flipcarousel").hasClass("flipped")) {
                                fadeFront(settings.elements.main.find(".flipcarousel"));
                            }
                            else {
                                fadeBack(settings.elements.main.find(".flipcarousel"));
                            }
                        }
                    });
                };

                function fadeFront(ctx) {
                    ctx.removeClass("flipped");
                    settings.elements.back.fadeOut(function() {
                        settings.elements.front.fadeIn();
                        bindIE();
                        methods.changeAudioAndTranscript();
                        settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                        settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                        settings.elements.main.find(".flipcarousel").focus()
                    });
                };

                function fadeBack(ctx) {
                    ctx.addClass("flipped");
                    settings.elements.front.fadeOut(function() {
                        settings.elements.back.fadeIn();
                        bindIE();
                        methods.changeAudioAndTranscript();
                        settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                        settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                        settings.elements.main.find(".flipcarousel").focus()
                    });
                };
            }
        },

        bindNext: function() {
            bindNext();
            function bindNext() {
                settings.elements.next.bind("click", function() {
                    $(this).unbind("click");
                    $(this).unbind("keydown");
                    next();
                });
                settings.elements.next.bind("keydown", function(e) {
                    if (e.which == 13) {
                        $(this).unbind("keydown");
                        $(this).unbind("click");
                        next();
                    }
                });
            }

            function next() {
                if (settings.elements.count + 1 > settings.card.length)
                    settings.elements.count = 1;
                else
                    settings.elements.count++;

                methods.setCounterText();
                methods.updateCrumb();
                methods.flipAndChange("next");
            }
        },

        bindPrev: function() {
            bindPrev();
            function bindPrev() {
                settings.elements.prev.bind("click", function() {
                    $(this).unbind("click");
                    $(this).unbind("keydown");
                    prev();
                });
                settings.elements.prev.bind("keydown", function(e) {
                    if (e.which == 13) {
                        $(this).unbind("keydown");
                        $(this).unbind("click");
                        prev();
                    }
                });
            }

            function prev() {
                if (settings.elements.count - 1 < 1)
                    settings.elements.count = settings.card.length;
                else
                    settings.elements.count--;
                methods.setCounterText();
                methods.updateCrumb();
                methods.flipAndChange("prev");
            }
        },

        flipAndChange: function(btn) {
            if (methods.detectIE() == false) {
                if (settings.elements.main.find(".flipcarousel").hasClass("flipped")) {
                    settings.elements.front.html(settings.card[settings.elements.count-1].front.html);

                    settings.elements.main.find(".flipcarousel").children().first().transition({
                        perspective: '3000',
                        rotateX: '360deg',
                    });
                    settings.elements.main.find(".flipcarousel").children().last().transition({
                        perspective: '3000',
                        rotateX: '540deg',
                        complete: function() {

                            settings.elements.back.html(settings.card[settings.elements.count-1].back.html);
                            if (btn == "next") {
                                methods.bindNext();
                            }
                            else if (btn == "prev") {
                                methods.bindPrev();
                            }
                            else if (btn = "thumb") {
                                methods.bindCrumbClick();
                            }

                            settings.elements.main.find(".flipcarousel").removeClass("flipped")
                            if (settings.usingAudio) {
                                methods.changeAudioAndTranscript();
                                settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                                settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                            }
                            settings.elements.main.find(".flipcarousel").focus()
                            methods.setHeight()
                        }
                    });
                }
                else {
                    settings.elements.main.find(".flipcarousel").css("transform", "perspective(3000px) rotateY(0deg)")
                    settings.elements.back.children().hide();
                    settings.elements.main.find(".flipcarousel").one().transition({
                        perspective: '3000px',
                        rotateY: '360deg',
                        complete: function() {
                            if (settings.flippable)
                                settings.elements.back.html(settings.card[settings.elements.count-1].back.html);
                            $(this).css("transform", "perspective(3000px) rotateY(0deg)");
                            settings.elements.back.children().show();
                            if (btn == "next") {
                                methods.bindNext();
                            }
                            else if (btn == "prev") {
                                methods.bindPrev();
                            }
                            else if (btn = "thumb") {
                                methods.bindCrumbClick();
                            }
                            if (settings.usingAudio) {
                                methods.changeAudioAndTranscript();
                                settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                                settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                            }
                            settings.elements.main.find(".flipcarousel").focus()
                            methods.setHeight()
                        }
                    })
                    settings.elements.front.html(settings.card[settings.elements.count-1].front.html);
                }
            }
            else {
                if (settings.elements.main.find(".flipcarousel").hasClass("flipped")) {
                    settings.elements.main.find(".flipcarousel").removeClass("flipped");
                    if (btn == "next") {
                        settings.elements.back.animate({marginLeft: 15}, {queue: false})
                    }
                    if (btn == "prev") {
                        settings.elements.back.animate({marginLeft: "-15"}, {queue: false})
                    }
                    settings.elements.back.fadeOut(function() {
                        settings.elements.front.html(settings.card[settings.elements.count-1].front.html);
                        settings.elements.back.html(settings.card[settings.elements.count-1].back.html);
                        settings.elements.front.fadeIn();
                        settings.elements.back.css("marginLeft", 0);
                        if (btn == "next") {
                            methods.bindNext();
                        }
                        else if (btn == "prev") {
                            methods.bindPrev();
                        }
                        else if (btn = "thumb") {
                            methods.bindCrumbClick();
                        }
                        if (settings.usingAudio) {
                            methods.changeAudioAndTranscript();
                            settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                            settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                        }
                        settings.elements.main.find(".flipcarousel").focus()
                        methods.setHeight()
                    });
                }
                else {
                    if (btn == "next") {
                        settings.elements.front.animate({marginLeft: 15}, {queue: false})
                    }
                    if (btn == "prev") {
                        settings.elements.front.animate({marginLeft: "-15"}, {queue: false})
                    }
                    settings.elements.front.fadeOut(function() {
                        settings.elements.front.html(settings.card[settings.elements.count-1].front.html);
                        if (settings.flippable)
                            settings.elements.back.html(settings.card[settings.elements.count-1].back.html);
                        settings.elements.front.fadeIn();
                        settings.elements.front.css("marginLeft", 0);
                        if (btn == "next") {
                            methods.bindNext();
                        }
                        else if (btn == "prev") {
                            methods.bindPrev();
                        }
                        else if (btn = "thumb") {
                            methods.bindCrumbClick();
                        }
                        if (settings.usingAudio) {
                            methods.changeAudioAndTranscript();
                            settings.elements.main.find(".flipcarousel-transcript-text").hide().removeClass("visible")
                            settings.elements.main.find(".flipcarousel-transcript-btn").children().removeClass("active")
                        }
                        settings.elements.main.find(".flipcarousel").focus()
                        methods.setHeight()
                    });
                }
            }
        },

        bindScreenReaderEvents: function() {
            settings.elements.main.find(".flipcarousel-crumb").focus(function() {
                settings.elements.screenReader.html("Click this to jump to card " + $(this).data('crumb') + "")
            })
            settings.elements.main.find(".flipcarousel").focus(function() {
                if ($(this).hasClass("flipped")) {
                    settings.elements.screenReader.html(settings.elements.main.find(".flipcarousel-back").html())
                }
                else {
                    settings.elements.screenReader.html(settings.elements.main.find(".flipcarousel-front").html())
                }
            })
            settings.elements.main.find(".flipcarousel").blur(function() {
                settings.elements.main.find(".mejs-playpause-button").children().attr("tabindex","0").attr("aria-labelledby", "flipcarousel-screen-reader-' + settings.elements.main.attr('id') + '").focus();
                settings.elements.screenReader.html("Click this play button to listen to the audio")
            });
        }
    }

    return this.each(function() {
        settings.elements = {};
        settings.elements.main = $(this).addClass("flipcarousel-activity");
        methods.init(this);
    });
};
