jQuery.fn.activityMatching = function(options) {

    var defaults = {
        'mode' : "dropDowns", //Supported types are: "dropDowns" "leftRight"
        'title' : 'Activity',
        'icon' : 'tqi-activity',
        'instructions' : 'Select the correct answer from each dropdown, click the "Check" button to test your answers',
        "feedback" : "Congratulations, you've completed the activity. Click the reset button below to start again.",
        'randomiseQuestions' : true, // Only works with dropDowns mode
        'randomiseAnswers': true,
        'backgroundImage' : false,
        'backgroundPosition' : 'left',
        'textColor' : false,
        'textShadow' : false,
        'iconFeedback' : true,
        'highlightRows' : false
    };

    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            methods.createElements();
            methods.buildWrappers();
            methods.buildCounter();
            methods.checkCssChanges();
            methods.buildShell();
            methods.addEventListeners();
        },

        createElements: function() { // Creating elelments for global use.

            if (settings.insidePanel == true) {
                settings.elements.headerRow = $("<div class='maHeaderBg maHeaderBgInsidePanel'>")
                settings.elements.headings = $("<div class='maHeadingContainer maHeadingContainerInsidePanel'><div class='maHeadingDiv pull-left'><p class='maHeading'>"+ settings.columns[0].columnHeading +"</p></div><div class='maHeadingDiv pull-right'><p class='maHeading'>"+ settings.columns[1].columnHeading +"</p></div><div class='clearfix maHeadingBorder'></div></div></div>");
                settings.elements.shell = $("<div class='maQuestionWrapper maQuestionWrapperInsidePanel'></div>");
                settings.elements.footerCol = $("<div><div class='maFooter maFooterInsidePanel col-md-12'></div>");
                settings.elements.feedbackContainer = $("<div class='maFeedbackContainer maFeedbackContainerInsidePanel'></div>");
            }
            else {
                settings.elements.headerRow = $("<div class='maHeaderBg'>")
                settings.elements.headings = $("<div class='maHeadingContainer'><div class='maHeadingDiv pull-left'><p class='maHeading'>"+ settings.columns[0].columnHeading +"</p></div><div class='maHeadingDiv pull-right'><p class='maHeading'>"+ settings.columns[1].columnHeading +"</p></div><div class='clearfix maHeadingBorder'></div></div></div>");
                settings.elements.shell = $("<div class='maQuestionWrapper'></div>");
                settings.elements.footerCol = $("<div><div class='maFooter'></div>");
                settings.elements.feedbackContainer = $("<div class='maFeedbackContainer'></div>");
            }
            settings.elements.wrap = $("<div>")
            settings.elements.header = $("<div class='maHeader'><i class='tqi " + settings.icon + " tqi-2x tq-panel-icon pull-left'></i>" + "<h3 class='panel-title'> " + settings.title + "</h3></div>");
            settings.elements.instructions = $("<p tabindex='0' class='mA-instructions'>" + settings.instructions + "</p>")
            settings.elements.questionsWrap = $("<div></div>");


            settings.elements.qWrap = $("<div class='clearfix'>")
            settings.elements.footerRow = $("<div class='row'></div>")

            settings.elements.footerContainer = $("<div class='row'><div class='footerBG'></div></div>");

            settings.elements.countContainer = $("<div tabindex='0' class='maCountContainer'></div>");
            settings.elements.correctCount = $("<p class='maCorrectCount'></p>");
            settings.elements.incorrectCount = $("<p class='maIncorrectCount'></p>");

            settings.elements.button = $("<button class='btn maFeedbackBtn btn-default'>");
            settings.elements.resetButton = $("<button class='btn maResetBtn btn-default'>");
        },

        buildWrappers: function() { // Building the containers
            settings.elements.main.append(settings.elements.wrap);

            settings.elements.wrap.append(settings.elements.headerRow);

            settings.elements.headerRow.append(settings.elements.header)
            settings.elements.headerRow.append(settings.elements.countContainer)
            settings.elements.headerRow.append(settings.elements.instructions)
            settings.elements.headerRow.append($("<div class='clearfix'>"))
            settings.elements.wrap.append(settings.elements.headings);
            settings.elements.wrap.append(settings.elements.questionsWrap);

        },

        buildCounter: function() {
            settings.elements.correctCount.html("Correct: 0");
            settings.elements.incorrectCount.html("Incorrect: 0");
            settings.elements.button.html("Check Answers <i class='fa fa fa-question-circle'></i>");
            settings.elements.countContainer.append(settings.elements.correctCount);
            settings.elements.countContainer.append(settings.elements.incorrectCount);
            settings.elements.footerCol.children().append(settings.elements.button)
            settings.elements.footerCol.children().append($("<div class='clearfix'>"))
        },

        checkCssChanges: function() { // Applying Css if the user has speciifed any
            if (settings.textColor)
                settings.elements.questionsWrap.css("color", settings.textColor);

            if (settings.textShadow)
                settings.elements.questionsWrap.css("text-shadow", settings.textShadow);

            if (settings.backgroundImage)
                settings.elements.questionsWrap.css("background", "url('" + settings.backgroundImage + "') no-repeat");

            if (settings.backgroundPosition)
                settings.elements.questionsWrap.css("background-position", settings.backgroundPosition);
        },

        buildShell: function() { // Building shell for questions to go in to
            var length = 0;
            var shellDiv;
            if (settings.mode == "dropDowns") {
                if (settings.insidePanel == true)
                    shellDiv = $("<div><div class='questionBG questionBGInsidePanel'><div class='maQuestionDiv maQuestionDivInsidePanel col-xs-12 col-md-9'></div><div class='maQuestionDiv col-xs-12 col-md-3'></div></div></div>");
                else
                    shellDiv = $("<div><div class='questionBG'><div class='maQuestionDiv col-xs-12 col-md-9'></div><div class='maQuestionDiv col-xs-12 col-md-3'></div></div></div>");
            }
            else if (settings.mode == "leftRight") {
                if (settings.insidePanel == true)
                    shellDiv = $("<div><div class='questionBG questionBGInsidePanel'><div class='maQuestionDiv maQuestionDivInsidePanel col-xs-12 col-md-6'></div><div class='maQuestionDiv col-xs-12 col-md-6'></div></div></div>");
                else
                    shellDiv = $("<div><div class='questionBG'><div class='maQuestionDiv col-xs-12 col-md-6'></div><div class='maQuestionDiv col-xs-12 col-md-6'></div></div></div>");
            }

            var zebraTotal = 0;

            if (settings.mode == "dropDowns") {
                length = settings.questions.length;
                for (var i = 0; i < length; i++) {
                    settings.elements.shell.append(shellDiv.clone().append($("<div class='clearfix maQuestionBorder'>")));
                }
                methods.buildDropDownsQuestions(length);
            }

            else if (settings.mode == "leftRight") {
                length = settings.choices.length;
                for (var i = 0; i < length; i++) {
                    settings.elements.shell.append(shellDiv.clone().append($("<div class='clearfix maQuestionBorder'>")));
                }
                methods.buildLeftRightQuestions(length);
            }


            settings.elements.questionsWrap.append(settings.elements.shell);
            settings.elements.shell.append(settings.elements.footerCol);
        },

        buildDropDownsQuestions: function(length) {
            var shellDivs = settings.elements.shell.children().children().children();
            var questionArr = [];
            var answerArr = [];
            var questionCount = 0;
            for (var i = 0; i < length; i++) {
                questionArr[i] = $("<span tabindex='0'>" + settings.questions[i].question + "</span>").attr("data-answer-index", i);
                answerArr[i] = $("<option>" + settings.questions[i].answer + "</option>").attr("data-answer-index", i);
            }

            if (settings.randomiseQuestions)
                methods.randomise(questionArr);

            if (settings.randomiseAnswers)
                methods.randomise(answerArr);

            for (var i = 0; i < shellDivs.length; i++) {
                var select = $("<label><select class='maDropDowns' data-num='"+questionCount+"'></select></label>");
                select.children().append($("<option>Select Answer</option>"))
                if (i % 2 == 0) {
                    $(shellDivs[i]).html(questionArr[questionCount]);
                    questionCount++;
                }
                else {
                    for (var x = 0; x < length; x++) {
                        select.children().append(answerArr[x]);
                    }
                    $(shellDivs[i]).html(select.clone());
                }
            }
            settings.elements.shell.append($("<div class='clearfix'>"))
            settings.elements.shell.append(settings.elements.footerCol);
        },

        buildLeftRightQuestions: function(length) {
            var shellDivs = settings.elements.shell.children().children().children();
            var choicesArr = [];
            var choiceCount = 0;
            for (var i = 0; i < length; i++) {
                choicesArr[i] = settings.choices[i].choice;
            }

            $(shellDivs).each(function(i) {
                if (i % 2 == 0) {
                    $(shellDivs[i]).addClass("maLeftRightDiv")
                    $(shellDivs[i]).html
                    ("<fieldset><legend class='sr-only'></legend><span tabindex='0' class='mobileRadio'>" + choicesArr[choiceCount] + "</span>" + "<div class='visible-sm visible-xs clear'></div><span class='mobileRadioHeading visible-sm visible-xs'>"+ settings.columns[0].columnHeading +"</span><label><p class='sr-only'>Select</p><input data-col-num='0' title='"+settings.columns[0].columnHeading+"' data-num='"+choiceCount+"' class='leftRightSelector' type='checkbox' val="+ settings.choices[choiceCount].side +" name="+ choiceCount +"></label></fieldset>")

                    $(shellDivs[i+1]).html
                    ("<fieldset><legend class='sr-only'></legend><span tabindex='0' class='mobileRadio mobileRadioHide'>" + choicesArr[choiceCount] + "</span>" + "<div class='visible-sm visible-xs clear'></div><span class='mobileRadioHeading visible-sm visible-xs'>"+ settings.columns[1].columnHeading +"</span><label><p class='sr-only'>Select</p><input data-col-num='1' title='"+settings.columns[1].columnHeading+"' data-num='"+choiceCount+"' class='leftRightSelector' type='checkbox' val="+ settings.choices[choiceCount].side +" name="+ choiceCount +"></label></fieldset>")
                    choiceCount++;
                }
            })

            settings.elements.shell.append($("<div class='clearfix'>"))
        },

        addEventListeners: function() {
            settings.elements.button.on("click", function() {
                settings.elements.countContainer.focus()
                settings.elements.main.find($(".maFeedbackIcon")).remove()
                if (settings.mode == "dropDowns")
                    methods.checkDropDownsAnswers();
                if (settings.mode == "leftRight")
                    methods.checkLeftRightAnswers();
            });

            settings.elements.resetButton.on("click", function() {1
                methods.reset();
            });
        },

        checkDropDownsAnswers: function() {
            var shellChildren = settings.elements.shell.find(".questionBG").children()
            var spanIndex = [];
            var selectedIndex = [];
            var spanCount = 0;
            var selectCount = 0;

            shellChildren.each(function(i) {
                if (i % 2 == 0) {
                    spanIndex[spanCount] = $(this).children().data("answer-index");
                    spanCount++;
                }
                else {
                    $(this).children().children().children().each(function(x) {
                        if ($(this).is(":selected")) {
                            selectedIndex[selectCount] = $(this).data("answer-index");
                            if ($(this).data("answer-index") == spanIndex[selectCount]) {
                                $(this).parent().attr('title', 'This is Correct')
                            }
                            else {
                                $(this).parent().attr('title', 'This is Incorrect')
                            }
                        }
                    })
                    selectCount++;
                }
            })
            methods.addFeedback(spanIndex, selectedIndex)
        },

        checkLeftRightAnswers: function() {
            var inputArr = settings.elements.questionsWrap.find("input");
            var correctArr = [];
            var selectedArr = [];
            var count = 0;
            inputArr.each(function(i) {
                if (i % 2 == 0) {
                    correctArr[count] = 0;
                    selectedArr[count] = 1;
                    count++;
                }
            });
            inputArr.each(function(i) {
                if (i % 2 == 0) {
                    if ($(inputArr[i]).is(":checked") && $(inputArr[i+1]).is(":checked")) {
                        correctArr[$(this).data("num")] = $(inputArr[i]).attr('val')
                        selectedArr[$(this).data("num")] = "both";
                        if ($(inputArr[i]).attr('val') == 'both' && $(inputArr[i+1]).attr('val') == 'both') {
                           $(inputArr[i]).attr('title', "This is Correct.")
                           $(inputArr[i+1]).attr('title', "This is Correct.")
                        }
                        else {
                            $(inputArr[i]).attr('title', "This is Incorrect.")
                            $(inputArr[i+1]).attr('title', "This is Incorrect.")
                        }
                    }
                    else if ($(inputArr[i]).is(":checked")) {
                        correctArr[$(this).data("num")] = $(inputArr[i]).attr('val')
                        selectedArr[$(this).data("num")] = "left";
                        if ($(inputArr[i]).attr('val') == 'left') {
                            $(inputArr[i]).attr('title', "This is Correct.")
                        }
                        else {
                            $(inputArr[i]).attr('title', "This is Incorrect.")
                        }
                    }
                    else if ($(inputArr[i+1]).is(":checked")) {
                        correctArr[$(this).data("num")] = $(inputArr[i]).attr('val')
                        selectedArr[$(this).data("num")] = "right";
                        if ($(inputArr[i+1]).attr('val') == 'right') {
                            $(inputArr[i+1]).attr('title', "This is Correct.")
                        }
                        else {
                            $(inputArr[i+1]).attr('title', "This is Incorrect.")
                        }
                    }
                }
            })
            methods.addFeedback(correctArr, selectedArr)
        },

        addFeedback: function(correct, selected) {
            var correctCount = 0;
            var incorrectCount = 0;
            var rows = [];
            var rowCount = 0;
            var incorrectIcon = $("<i class='maFeedbackIcon maIconIncorrect col-md-1 col-xs-1 fa fa-2x fa-times'></i>");
            var correctIcon = $("<i class='maFeedbackIcon maIconCorrect col-md-1 col-xs-1 fa fa-2x fa-check'></i>");
            var feedbackArr = [];

            settings.elements.main.find('.questionBG').each(function(i) {
                rows[rowCount] = settings.elements.shell.find('.questionBG').eq(i)
                rowCount++;
            })

            var iconDivs = settings.elements.main.find('.maQuestionDiv')

            if (settings.iconFeedback) {
                iconDivs.each(function(i) {
                    if (settings.mode == "dropDowns") {
                        if (i % 2 == 0) {
                            $(this).removeClass("col-md-9")
                            $(this).addClass("col-md-8")
                            $(this).removeClass("col-xs-12")
                            $(this).addClass("col-xs-11")
                        }
                        else {
                           $(this).removeClass("col-xs-12")
                           $(this).addClass("col-xs-11 col-xs-offset-1 col-md-offset-0")
                        }
                    }
                    else if (settings.mode == "leftRight") {
                        if (i % 2 == 0) {
                            $(this).removeClass("col-md-6")
                            $(this).addClass("col-md-5")
                            $(this).removeClass("col-xs-12")
                            $(this).addClass("col-xs-11")
                        }
                        else {
                           $(this).removeClass("col-xs-12")
                           $(this).addClass("col-xs-11 col-xs-offset-1 col-md-offset-0")
                        }
                    }
                })
            }

            $(correct).each(function(i) {
                if (correct[i] == selected[i]) {
                    feedbackArr[i] = "Question" + (i + 1) + " Correct";
                    correctCount++;
                    if (settings.highlightRows)
                        $(rows[i]).parent().css("background-color", "#288511");
                    if (settings.iconFeedback)
                        $(rows[i]).first().prepend(correctIcon.clone())
                }
                else {
                    feedbackArr[i] = "Question" + (i + 1) + " Incorrect";
                     incorrectCount++;
                    if (settings.highlightRows)
                        $(rows[i]).parent().css("background-color", "#cc3300");
                    if (settings.iconFeedback)
                        $(rows[i]).first().prepend(incorrectIcon.clone())
                }
            })

            settings.elements.correctCount.html("Correct: " + correctCount);
            settings.elements.incorrectCount.html("Incorrect: " + incorrectCount);

            if (correctCount == correct.length) {
                var footer = settings.elements.footerCol.detach()
                settings.elements.headings.remove()
                settings.elements.questionsWrap.html(settings.elements.feedbackContainer);
                settings.elements.feedbackContainer.html("<p><b>" + settings.feedback + "</b></p>");
                settings.elements.feedbackContainer.append(footer)

                settings.elements.footerCol.find(settings.elements.button).remove();
                settings.elements.resetButton.html("Reset <i class='fa fa-refresh'></i>");
                settings.elements.footerCol.children().append(settings.elements.resetButton);
                settings.elements.feedbackContainer.append($("<div class='clearfix'>"))
            }
        },

        reset: function() {
            settings.elements.wrap.remove();
            methods.init();
        },

        randomise: function(arr) {
            for (var j, x, i = arr.length; i; j = parseInt(Math.random() * i), x = arr[--i], arr[i] = arr[j], arr[j] = x);
            return arr;
        },
    } // End Methods

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        $(this).addClass("matchingActivity")
        $(this).parent().addClass('matchingPanel')
        methods.init(this)
    });
};
