jQuery.fn.tqTooltip = function(options) {
 
    var defaults = {
        title: "", // Header / Title for popup
        content: "", // The HTML or text for the popup
        position: "top", // Top || Bottom || Left || Right
        animation: true, // True || False - Adds a fade in out animation to tooltip
        delay: 0, // Applies a delay to the fade in and out event or show/hide depending if animation is set to true
        trigger: 'click', // Click || Hover || Focus (Blur) 
    };
 
    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            methods.createElements()
            methods.buildStage()
            methods.buildTriggers()
            methods.addAttributes()
            methods.keyboardAccessibility()
        },

        createElements: function() {
            settings.elements.stage = $("<div class='tq-tooltip-stage'>")
            settings.elements.trigger = $("<i class='tq-tooltip-trigger'>")
            settings.elements.triggerStack = $("<span class='tq-tooltip-trigger'>")
        },

        buildStage: function() {
            settings.elements.main.append(settings.elements.stage)
            settings.elements.stage.css(settings.stage.css)
        },

        buildTriggers: function() {
            for (var i = 0; i < settings.tooltips.length; i++) {
                if (settings.tooltips[i].stackedClass == undefined) {
                    settings.elements.stage.append(settings.elements.trigger.clone()
                        .addClass(settings.tooltips[i].triggerClass)
                        .css(settings.tooltips[i].css))
                }
                else {
                    settings.elements.stage.append(settings.elements.triggerStack.clone().addClass(settings.tooltips[i].stackedWrapClass)
                        .append(settings.elements.trigger.clone().removeClass('tq-tooltip-trigger')
                            .addClass(settings.tooltips[i].stackedClass))
                        .append(settings.elements.trigger.clone().removeClass('tq-tooltip-trigger')
                            .addClass(settings.tooltips[i].triggerClass))
                        .css(settings.tooltips[i].css))
                }
            }
        },

        addAttributes: function() {
            settings.elements.main.find('.tq-tooltip-trigger').each(function(i) {
                $(this).attr('data-html', 'true') // HTML

                $(this).attr('tabindex', '0') // Tab Index - Screen Reader Support
                $(this).attr('aria-label', "" + settings.tooltips[i].title + ", " + settings.tooltips[i].content) // Aria Label - Screen Reader Support

                $(this).attr('title', settings.tooltips[i].title) // Popver Title
                $(this).attr('data-content', settings.tooltips[i].content) // Popover Content
                $(this).attr('data-placement', settings.tooltips[i].position) // Popover Position

                // Apply these attributes if they're not the default values
                if (settings.tooltips[i].animation != true)
                    $(this).attr('data-animation', settings.tooltips[i].animation) // Animation
                if (settings.tooltips[i].delay > 0)
                    $(this).attr('data-delay', settings.tooltips[i].delay) // Delay
                if (settings.tooltips[i].trigger != 'click')
                    $(this).attr('data-trigger', settings.tooltips[i].trigger) // Trigger

                $(this).popover() // Calling bootstrap popover.js
            })
        },

        keyboardAccessibility: function() {
            settings.elements.main.find('.tq-tooltip-trigger').on('keydown', function(e) {
                if (e.which == 13) {
                    if ($($(this).popover()[0].nextSibling).hasClass('popover')) {
                        $.noop
                    }
                    else {
                        settings.elements.main.find('.tq-tooltip-trigger').popover('hide')
                        $(this).popover('toggle')

                    }
                    methods.addAttributes()
                    
                }
            })
        },
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        methods.init(this)
    });
};