jQuery.fn.zoomPan = function(options) {
 
    var defaults = {
        maxWidth: '600px',
        scaleMax: 3,
        scaleMin: 1,
        scale: 1,
        mouseX: 0,
        mouseY: 0,
        trigger: 'middle'
    };
 
    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            methods.createElements()
            methods.buildElements()
            methods.bindHammerEvents()
            methods.bindClickEvents()
            methods.bindScrollEvents()
            methods.bindMouseOverEvents()
            // methods.bindResizeEvent()
        },

        createElements : function() {
            settings.elements.imageContainer = $("<div aria-hidden='true' class='zoomPanImgContainer'>")
            settings.elements.image = $("<img class='img-responsive zoomPanImg' src=" + settings.image + ">")
            settings.elements.image.attr('id', settings.elements.main.attr('id') + "_image")
            settings.elements.imageAttribution = $("<div aria-hidden='true' class='col-md-12 imageAttribution'><p></p></div>")
            settings.elements.zoomInBtn = $('<button class="btn btn-primary zoomBtn zoomInBtn">+</button>');
            settings.elements.zoomOutBtn = $('<button class="btn btn-primary zoomBtn zoomOutBtn">-</button>'); 
            settings.elements.scrRdrDiv = $("<div class='sr-only'>")
        },

        buildElements: function() {
            settings.elements.main.append(settings.elements.imageContainer)
            settings.elements.imageContainer.append(settings.elements.image)
            settings.elements.main.append(settings.elements.imageAttribution)
            settings.elements.imageAttribution.children().html("Image: " + settings.attribution)

            settings.elements.image.load(function() { // Waiting for image to load to get dimensions
                settings.elements.imageContainer.css('maxWidth',settings.maxWidth)
                settings.elements.imageAttribution.children().css('maxWidth',settings.maxWidth)
            })

            settings.elements.imageContainer.append(settings.elements.zoomInBtn)
            settings.elements.imageContainer.append(settings.elements.zoomOutBtn)

            if (settings.altText) {
                settings.elements.main.append(settings.elements.scrRdrDiv)
                settings.elements.scrRdrDiv.html(settings.altText)
            }
        },

        bindHammerEvents: function() { // Hammer JS for multi touch events
            var mainId = settings.elements.main.attr('id')            
            var myElement = document.getElementById(mainId + '_image');

            var hammer = new Hammer.Manager(myElement, {
                recognizers: [
                    [Hammer.Rotate],
                    [Hammer.Tap, { taps: 2 }], // Double Tap
                    [Hammer.Pinch, { enable: true }, ['rotate']],
                ]
            });

            hammer.on("pinchin", function(ev) {
                methods.zoomOut(true)
            });

            hammer.on("pinchout", function(ev) {
                methods.zoomIn(true)
            });

            hammer.on("tap", function(ev) {
                methods.zoomIn()
            });
        },

        bindClickEvents: function() {
            settings.elements.zoomInBtn.on('click', function() {
                settings.trigger = 'middle'
                methods.zoomIn()
            });

            settings.elements.zoomOutBtn.on('click', function() {
                settings.trigger = 'middle'
                methods.zoomOut()
            });
        },

        bindScrollEvents: function() {
            settings.elements.image.bind('mousewheel', function(e) {
                e.preventDefault();

                if(e.originalEvent.deltaY < 0 || e.originalEvent.wheelDelta > 0) {
                    methods.zoomIn()
                }

                else if (e.originalEvent.deltaY > 0 || e.originalEvent.wheelDelta < 0) {
                    methods.zoomOut()
                }
            });

            settings.elements.image.bind('wheel', function(e){
                e.preventDefault();

                if(e.originalEvent.deltaY < 0 || e.originalEvent.wheelDelta > 0) {
                    methods.zoomIn()
                }

                else if (e.originalEvent.deltaY > 0 || e.originalEvent.wheelDelta < 0) {
                    methods.zoomOut()
                }
            })
        },

        bindMouseOverEvents: function() {
            settings.elements.imageContainer.hover(function() {
                $(this).addClass('isHover')
            }, function() {
                $(this).removeClass('isHover')
            })

            settings.elements.imageContainer.bind('mousemove',function(e){ 
                if (e.offsetX == undefined) { // Firefox support _
                    settings.mouseX = e.originalEvent.layerX
                }

                else {
                    settings.mouseX = e.offsetX
                }

                if (e.offsetY == undefined) { // Firefox support _
                    settings.mouseY = e.originalEvent.layerY
                }

                else {
                    settings.mouseY = e.offsetY
                }
                methods.closestTrigger()
            }); 
        },

        // bindResizeEvent: function() {
        //     $(window).resize(function() {
        //         settings.elements.image.css('top','0px').css('left','0px')
        //     })
        // },

        addDraggable : function() { 
            var imageWidth = settings.elements.image.innerWidth();
            var imageHeight = settings.elements.image.innerHeight();
            var containerWidth = settings.elements.imageContainer.innerWidth()
            var containerHeight = settings.elements.imageContainer.innerHeight()

            var x1 = Math.round(((imageWidth * (settings.scale)) - containerWidth) / 2)
            var y1 = Math.round(((imageHeight * (settings.scale)) - containerHeight) / 2)
            var x2 = "-" + x1
            var y2 = "-" + y1

            settings.elements.image.draggable({
                drag: function( event, ui ) {
                    if (ui.position.top >= y1) {
                        ui.position.top = y1
                    }
                    if (ui.position.left >= x1) {
                        ui.position.left = x1
                    }
                    if (ui.position.top <= y2) {
                        ui.position.top = y2
                    }
                    if (ui.position.left <= x2) {
                        ui.position.left = x2
                    }
                }
            })
        },

        zoomIn: function(pinch) { // Incrementing scale depending on event
            if (settings.scale >= settings.scaleMax) {
                return false;
            }

            else if (pinch == true) {
                settings.scale += 0.15
            }

            else {
                settings.scale += 0.6
            }
            methods.zoomAnimate()
        },

        zoomOut: function(pinch) { // Decrementing scale depending on event
            if (settings.scale <= settings.scaleMin) {
                return false;
            }

            else if (pinch == true) {
                settings.scale -= 0.15
            }

            else {
                settings.scale -= 0.6
            }
            methods.zoomAnimate()
        },

        zoomAnimate : function() {
            var imageWidth = settings.elements.image.innerWidth();
            var imageHeight = settings.elements.image.innerHeight();
            var containerWidth = settings.elements.imageContainer.innerWidth()
            var containerHeight = settings.elements.imageContainer.innerHeight()

            var x1 = Math.round(((imageWidth * (settings.scale)) - containerWidth) / 2)
            var y1 = Math.round(((imageHeight * (settings.scale)) - containerHeight) / 2)
            var x2 = Math.round("-" + x1)
            var y2 = Math.round("-" + y1)

            if (settings.trigger == 'middle') {
                animate(0, 0)
            }

            else if (settings.trigger == 'topLeft') {
                animate(x1, y1)
            }

            else if (settings.trigger == 'bottomRight') {
                animate(x2, y2)
            }

            else if (settings.trigger == 'bottomLeft') {
                animate(x1, y2)
            }

            else if (settings.trigger == 'topRight') {
                animate(x2, y1)
            }

            function animate(x,y) {
                settings.elements.image.animate({
                    scale: settings.scale,
                    left: x,
                    top: y,
                    complete: methods.addDraggable(),
                }, {duration: 500, queue: false, ease: 'easeinout'})

            }
        },

        closestTrigger: function() {
            var mouseX = settings.mouseX
            var mouseY = settings.mouseY

            var containerWidth = settings.elements.imageContainer.width(); // Top Right
            var containerHeight = settings.elements.imageContainer.height(); // Bottom Left

            var halfWidth = containerWidth / 2;
            var halfHeight = containerHeight / 2;

            if (containerWidth - mouseX < (containerWidth - 150) && containerWidth - mouseX > (150) && containerHeight - mouseY < (containerHeight - 150) && containerHeight - mouseY > (150)) {
                settings.trigger = 'middle'
            }

            else if (containerWidth - mouseX > halfWidth && containerHeight - mouseY > halfHeight) {
                settings.trigger = 'topLeft'
            }

            else if (containerWidth - mouseX < halfWidth && containerHeight - mouseY > halfHeight) {
                settings.trigger = 'topRight'
            }

            else if (containerWidth - mouseX > halfWidth && containerHeight - mouseY < halfHeight) {
                settings.trigger = 'bottomLeft'
            }

            else if (containerWidth - mouseX < halfWidth && containerHeight - mouseY < halfHeight) {
                settings.trigger = 'bottomRight'
            }
        },
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        methods.init(this)
    });
};