jQuery.fn.sortableList = function(options) {

    var defaults = {
        title: "Activity",
        icon: "tqi-activity",
        instructions: "Instructions",
        mode: "keepCorrect",
        showHeadings: true,
        swapDraggables: false,
        shuffleAnswersButton: false,
        singleRow: false,
        shuffleDraggables: true,
        shuffleStatics: true,
        draggableCols: "4",
        staticCols: "8",
        doubleStatic: false,
    };

    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            settings.elements.header = $('<div class="sortable-panel-header">')
            settings.elements.headerTitleIconWrapper = $('<div class="header-col-wrap"></div>')
            settings.elements.headerIcon = $('<i aria-hidden="true" class="tqi ' + settings.icon + ' tqi-2x tq-panel-icon pull-left"></i>') // Create TQ Icon
            settings.elements.headerTitle = $('<h3>' + settings.title + '</h3>') // Create H3 Element
            settings.elements.headerInstructions = $('<p>' + settings.instructions + '</p>') // Create Instructions Element

            settings.elements.wrapRow = $("<div>");
            settings.elements.mobileMessage = $("<div class='mobileMessage hidden-xs hidden-sm col-md-12 hidden-md hidden-lg'><h2>View this activity on a larger screen.</h2></div>");
            settings.elements.wrap = $("<div class='sortable-main-wrap'>");
            settings.elements.headerRow = $("<div>");
            settings.elements.headingRow = $("<div>");
            settings.elements.sortableRow = $("<div class='sortableRows'>");
            settings.elements.footerRow = $("<div class='footerRow'>");
            settings.elements.headings = $("<div class='sortable_headings'>");

            if (settings.singleRow == true)
                settings.elements.draggables = $("<ul class='col-md-12 draggableObj'>");
            else
                settings.elements.draggables = $("<ul class='sortableListUL col-xs-"+settings.draggableCols+" col-sm-"+settings.draggableCols+" col-md-"+settings.draggableCols+" draggableObj'>");

            settings.elements.statics = $("<ul class='sortableListUL col-xs-"+settings.staticCols+" col-sm-"+settings.staticCols+" col-md-"+settings.staticCols+" staticObj'>");
            if (settings.doubleStatic)
                settings.elements.statics2 = $("<ul class='sortableListUL col-xs-"+settings.staticCols+" col-sm-"+settings.staticCols+" col-md-"+settings.staticCols+" staticObj'>");
            settings.elements.li = $('<li aria-live="assertive" aria-labelledby="ariaSortableScreenReader_'+settings.elements.main.attr('id')+'" class="ui-state-default sortableListItem">')
            settings.elements.scrRdr = $("<div aria-live='assertive' class='sr-only ariaSortableScreenReader' id='ariaSortableScreenReader_"+settings.elements.main.attr('id')+"'>")

            settings.elements.arrowContainer = $('<div class="sortableArrowContainer"></div>');
            settings.elements.upArrow = $('<i tabindex="0" aria-labelledby="ariaSortableScreenReader_'+settings.elements.main.attr('id')+'" href="javascript:void(0)" class="fa sortableArrows sortableUpArrow fa-chevron-circle-up"></i>');
            settings.elements.downArrow = $('<i tabindex="0" aria-labelledby="ariaSortableScreenReader_'+settings.elements.main.attr('id')+'" href="javascript:void(0)" class="fa sortableArrows sortableDownArrow fa-chevron-circle-down"></i>');

            settings.elements.feedbackContainer = $("<div class='col-md-8 feedbackContainer'>");
            settings.elements.buttonContainer = $("<div class='col-md-4 buttonContainer'>");
            settings.elements.shuffleButton = $("<button href='#"+settings.elements.main.attr('id')+"' class='shuffleBtn sortableBtn btn btn-default'>Shuffle</button>");
            settings.elements.button = $("<button class='sortableBtn btn btn-default'>Check Answers</button>");
            settings.elements.resetButton = $("<a href='#"+settings.elements.main.attr('id')+"' class='sortableBtn btn btn-default' aria-labelledby='ariaSortableScreenReader_"+settings.elements.main.attr('id')+"' class='btn btn-default'>Reset</a>");

            methods.buildWrap();
            methods.buildInstructions();
            if (settings.showHeadings == true && settings.singleRow != true) {
                methods.buildHeadings();
            }
            methods.buildListItems()
            methods.buildFooter()
            methods.addSortable()
            methods.bindEvents()
        },

        buildWrap : function() {
            settings.elements.main.append(settings.elements.header)
            settings.elements.header.append(settings.elements.headerTitleIconWrapper)
            settings.elements.headerTitleIconWrapper.append(settings.elements.headerIcon) // Apend TQ Icon
            settings.elements.headerTitleIconWrapper.append(settings.elements.headerTitle) // Append H3
            settings.elements.header.append($("<div class='clearfix'>"))
            settings.elements.main.append(settings.elements.wrapRow)
            settings.elements.wrap.append(settings.elements.scrRdr)
            settings.elements.main.append(settings.elements.mobileMessage)
            settings.elements.wrapRow.append(settings.elements.wrap)
        },

        buildInstructions : function() {
            settings.elements.scrRdr.html("Tap the arrows or use your keyboard arrows to re-order items. Tap the check answers button to submit your answers.")
            settings.elements.header.append($("<p aria-live='assertive' class='sortableInstructions' aria-labelledby='ariaSortableScreenReader_"+settings.elements.main.attr('id')+"' tabindex='0'>"+settings.instructions+"</p>"))
        },

        buildHeadings : function() {
            settings.elements.wrap.append(settings.elements.headingRow)


            if (settings.doubleStatic) {
                var swappedCount = 2;
                for (var i = 0; i < 3; i++) {
                    if (settings.swapDraggables == true) {
                        if (i == 0) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("col-sm-"+settings.staticCols+" col-md-"+settings.staticCols+" col-xs-"+settings.staticCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[swappedCount].labelName + '</b></p>'))
                        }
                        if (i == 1) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("col-sm-"+settings.staticCols+" col-md-"+settings.staticCols+" col-xs-"+settings.staticCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[swappedCount].labelName + '</b></p>'))
                        }
                        if (i == 2) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("draggableHeading col-sm-"+settings.draggableCols+" col-md-"+settings.draggableCols+" col-xs-"+settings.draggableCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[swappedCount].labelName + '</b></p>'))
                        }

                        swappedCount--;
                    }
                    else {
                        if (i == 0) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("draggableHeading col-sm-"+settings.draggableCols+" col-xs-"+settings.draggableCols+" col-md-"+settings.draggableCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[i].labelName + '</b></p>'))
                        }
                        if (i == 1) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("col-sm-"+settings.staticCols+" col-xs-"+settings.staticCols+" col-md-"+settings.staticCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[i].labelName + '</b></p>'))
                        }
                        if (i == 2) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("col-sm-"+settings.staticCols+" col-xs-"+settings.staticCols+" col-md-"+settings.staticCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[i].labelName + '</b></p>'))
                        }
                    }
                }
            }
            else {
                var swappedCount = 1;
                for (var i = 0; i < 2; i++) {
                    if (settings.swapDraggables == true) {
                        if (i == 0) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("col-sm-"+settings.staticCols+" col-md-"+settings.staticCols+" col-xs-"+settings.staticCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[swappedCount].labelName + '</b></p>'))
                        }
                        if (i == 1) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("draggableHeading col-sm-"+settings.draggableCols+" col-md-"+settings.draggableCols+" col-xs-"+settings.draggableCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[swappedCount].labelName + '</b></p>'))
                        }
                        swappedCount--;
                    }
                    else {
                        if (i == 0) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("draggableHeading col-sm-"+settings.draggableCols+" col-xs-"+settings.draggableCols+" col-md-"+settings.draggableCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[i].labelName + '</b></p>'))
                        }
                        if (i == 1) {
                            settings.elements.headingRow.append(settings.elements.headings.clone().addClass("col-sm-"+settings.staticCols+" col-xs-"+settings.staticCols+" col-md-"+settings.staticCols+"").html('<p class="sortableGrayBg"><b>' + settings.headings[i].labelName + '</b></p>'))
                        }
                    }
                }
            }

            if (settings.singleRow != true) {
                var headings = settings.elements.main.find('.sortable_headings').children();
                headings.css('height',methods.getLargestHeight($(headings[0]),$(headings[1])))
            }
        },

        buildListItems : function() {
            if (settings.singleRow == true) {
                settings.elements.wrap.append(settings.elements.sortableRow.append(settings.elements.draggables))
            }
            else if (settings.doubleStatic) {
                if (settings.swapDraggables == true) {
                    settings.elements.wrap.append(settings.elements.sortableRow.append(settings.elements.statics).append(settings.elements.statics2).append(settings.elements.draggables))
                }
                else {
                    settings.elements.wrap.append(settings.elements.sortableRow.append(settings.elements.draggables).append(settings.elements.statics).append(settings.elements.statics2))
                }
            }
            else {
                if (settings.swapDraggables == true) {
                    settings.elements.wrap.append(settings.elements.sortableRow.append(settings.elements.statics).append(settings.elements.draggables))
                }
                else {
                    settings.elements.wrap.append(settings.elements.sortableRow.append(settings.elements.draggables).append(settings.elements.statics))
                }
            }

            var draggables = [];
            var statics = [];
            if (settings.doubleStatic)
                var statics2 = [];

            for (var i = 0; i < settings.items.length; i++) {
                draggables[i] = i;
                statics[i] = i;
            }

            if (!settings.shuffleAnswersButton) {
                if (settings.shuffleDraggables == true) {
                    draggables = methods.shuffle(draggables)
                }
                if (settings.shuffleStatics == true) {
                    statics = methods.shuffle(statics)
                    if (settings.doubleStatic)
                        statics2 = statics;
                }
                else {
                    statics2 = statics;
                }
            }

            for (var i = 0; i < settings.items.length; i++) {
                settings.elements.draggables.append(settings.elements.li.clone().addClass('sortableDraggableItem sortableDraggableItemBG').html('<span>' + settings.items[draggables[i]].labelName + '</span>').attr('data-num', settings.items[draggables[i]].rowMatch).attr('tabindex', '0'))
                if (settings.singleRow != true) {
                    settings.elements.statics.append(settings.elements.li.clone().addClass('sortableGrayBg').html(settings.rows[statics[i]].rowContents).addClass('sortableStaticItem').attr('data-num', statics[i]+1))
                    if (settings.doubleStatic)
                        settings.elements.statics2.append(settings.elements.li.clone().addClass('sortableGrayBg').html(settings.rows[statics2[i]].rowContents2).addClass('sortableStaticItem').attr('data-num', statics2[i]+1))
                }
            }

            if (settings.singleRow != true)
                methods.adjustItemHeight()

            settings.elements.main.find('.sortableDraggableItem').each(function(i) {
                $(this).append(settings.elements.arrowContainer.clone().append(settings.elements.upArrow.clone()).append(settings.elements.downArrow.clone()))
            })

            if (settings.shuffleAnswersButton) {
                settings.elements.main.find('.sortableDraggableItem').addClass('disableThis')
            }
        },

        buildFooter : function() {
            settings.elements.wrap.append(settings.elements.footerRow)
            settings.elements.footerRow.append(settings.elements.feedbackContainer)
            settings.elements.footerRow.append(settings.elements.buttonContainer)
            if (settings.shuffleAnswersButton) {
                settings.elements.buttonContainer.append(settings.elements.shuffleButton)
            }
            else {
                settings.elements.buttonContainer.append(settings.elements.button)
            }
            settings.elements.wrap.append($("<div class='clearfix'>"))
        },

        addSortable : function() {
            settings.elements.main.find('.draggableObj').sortable({
                axis: "y",
                stack: '.draggableObj li',
                containment: settings.elements.main,
                tolerance: "pointer",
                cancel: ".disableThis",
                start: function(event,ui){
                    settings.elements.main.find('.draggableObj li').each(function(i) {
                        $(this).removeClass('draggableItemHighlight alert alert-info').addClass('sortableDraggableItemBG')
                    })
                    ui.item.addClass('draggableItemHighlight alert alert-info').removeClass('sortableDraggableItemBG')
                    $('.disableThis', this).each(function(){
                        var $this = $(this);
                        $this.data('pos', $this.index());
                    });
                },
                change: function(event,ui){
                    $sortable = $(this);
                    $statics = $('.disableThis', this).detach();
                    $helper = $('<li></li>').prependTo(this);
                    $statics.each(function(){
                        var $this = $(this);
                        var target = $this.data('pos');
                        $this.insertAfter($('li', $sortable).eq(target));
                    });
                    $helper.remove();
                },
                stop: function() {
                    if (settings.singleRow != true)
                        methods.adjustItemHeight()
                }
            }).disableSelection()
        },

        bindEvents : function() {
            settings.elements.main.find(".shuffleBtn").on("click", function() {
                settings.shuffleAnswersButton = false;
                settings.shuffleDraggables = true;
                settings.shuffleStatics = false;
                methods.resetPlugin();
                settings.shuffleAnswersButton = true;
            });

            settings.elements.main.find(".sortableDraggableItem").on('click', function() {
                $(this).focus()
            });

            settings.elements.button.on('click', function() {
                methods.checkAnswers()
            });

            settings.elements.resetButton.on('click', function() {
                methods.resetPlugin()
            });

            settings.elements.main.find('.sortableUpArrow').click(function() {
                methods.keyboardInput(38,$(this).parent().parent())
            });

            settings.elements.main.find('.sortableDownArrow').on('click', function() {
                methods.keyboardInput(40,$(this).parent().parent())
            });

            settings.elements.main.find('.draggableObj li').keydown(function(e) {
                methods.keyboardInput(e, $(this))
            });

            window.addEventListener("keydown", function(e) {
                var focused = $(':focus');
                // space and arrow keys
                if([32, 37, 38, 39, 40].indexOf(e.which) > -1 && focused.hasClass('sortableDraggableItem')) {
                    e.preventDefault();
                }
            }, false);

            settings.elements.main.find('.draggableObj li').focus(function() {
                if ($(this).hasClass('disableThis')) {
                    $.noop
                }
                else {
                    settings.elements.main.find('.draggableObj li').each(function(i) {
                        $(this).removeClass('draggableItemHighlight alert alert-info').addClass('sortableDraggableItemBG')
                    })
                    $(this).addClass('draggableItemHighlight alert alert-info').removeClass('sortableDraggableItemBG')
                }
            })

            $(window).resize(function() {
                if (settings.singleRow != true) {
                    var headings = settings.elements.main.find('.sortable_headings').children();
                    headings.css('height','auto')
                    headings.css('height',methods.getLargestHeight(headings.eq(0),headings.eq(1)))
                }
                methods.adjustItemHeight()
            })
        },

        bindScreenReader : function() {
            var focused = $(":focus")
            methods.changeScrReaderText(focused,focused[0].attributes[5].value)
        },

        keyboardInput : function(e, ctx) {
            var incorrectArr = [];
            var count = 0;

            settings.elements.main.find('.draggableObj li').each(function(i) {
                if ($(this).hasClass('sortableIncorrectItem')) {
                    incorrectArr[count] = $(this).index()
                    count++;
                }
            });

            if ((e.which == 40 || e.which == 83 || e == 40) && ctx.hasClass('disableThis') != true) { // If down arrow key or s is pressed
                if (ctx.next().hasClass('sortableCorrectItem')) {
                    for (var i = ctx.index(); i < settings.items.length; i++) {
                        if (ctx.parent().children().eq(i).hasClass('sortableIncorrectItem')) {
                            for (var x = i+1; x < settings.items.length; x++) {
                                if (incorrectArr.indexOf(x) > -1) {
                                    ctx.parent().children().eq(x).insertAfter(ctx.parent().children().eq(i))
                                    ctx.parent().children().eq(i).insertAfter(ctx.parent().children().eq(x))
                                    ctx.parent().children().eq(x).focus()
                                    methods.adjustItemHeight()
                                    return false;
                                }
                            }
                        }
                    }
                }
                else {
                    ctx.next().after(ctx);
                    ctx.focus()
                    methods.adjustItemHeight()
                }
            }
            else if ((e.which == 38 || e.which == 87 || e == 38) && ctx.hasClass('disableThis') != true) { // If up arrow key or w is pressed
                if (ctx.prev().hasClass('sortableCorrectItem')) {
                    for (var i = ctx.index(); i >= 0; i--) {
                        if (ctx.parent().children().eq(i).hasClass('sortableIncorrectItem')) {
                            for (var x = i-1; x >= 0; x--) {
                                if (incorrectArr.indexOf(x) > -1) {
                                    ctx.parent().children().eq(x).insertBefore(ctx.parent().children().eq(i))
                                    ctx.parent().children().eq(i).insertBefore(ctx.parent().children().eq(x))
                                    ctx.parent().children().eq(x).focus()
                                    methods.adjustItemHeight()
                                    return false;
                                }
                            }
                        }
                    }
                }
                else {
                    ctx.prev().before(ctx);
                    ctx.focus()
                    methods.adjustItemHeight()
                }
            }
            methods.bindScreenReader()
        },

        checkAnswers : function() {
            var draggableIndex = [];
            var staticIndex = [];
            var feedback = false;
            var correctCount = 0;

            settings.elements.main.find('.draggableObj li').each(function(i) {
                draggableIndex[i] = $(this).data('num')
                $(this).removeClass('draggableItemHighlight alert alert-info').addClass('sortableDraggableItemBG')
            })
            settings.elements.main.find('.staticObj li').each(function(i) {
                staticIndex[i] = $(this).data('num')
            })

            for (var i = 0; i < draggableIndex.length; i++) {
                if (draggableIndex[i] == staticIndex[i] || draggableIndex[i] == i+1 && settings.singleRow == true) { // If correct
                    correctCount++;
                    if (settings.mode == "reset") {
                        settings.elements.main.find('.draggableObj li[data-num="'+draggableIndex[i]+'"]').removeClass('sortableIncorrectItem').addClass('sortableCorrectItem')
                        settings.elements.main.find('.draggableObj li').addClass('disableThis')
                        settings.elements.buttonContainer.html("").append(settings.elements.resetButton)
                        methods.bindEvents()
                    }
                    else if (settings.mode == "keepCorrect") {
                        settings.elements.main.find('.draggableObj li[data-num="'+draggableIndex[i]+'"]').removeAttr('tabindex').addClass('disableThis').removeClass('sortableIncorrectItem').addClass('sortableCorrectItem')
                    }
                }
                else { // If incorrect
                    if (settings.mode == "reset") {
                        settings.elements.main.find('.draggableObj li[data-num="'+draggableIndex[i]+'"]').removeClass('sortableCorrectItem').addClass('sortableIncorrectItem')
                        settings.elements.main.find('.draggableObj li').addClass('disableThis').removeAttr('tabindex')
                        settings.elements.main.find('.sortableArrows').addClass('disableThis').removeAttr('tabindex')
                        settings.elements.buttonContainer.html("").append(settings.elements.resetButton)
                        methods.bindEvents()
                    }
                    else if (settings.mode == "keepCorrect") {
                        settings.elements.main.find('.draggableObj li[data-num="'+draggableIndex[i]+'"]').removeClass('disableThis sortableCorrectItem').addClass('sortableIncorrectItem')
                    }
                }
            }

            if (correctCount == draggableIndex.length) {
                feedback = true;
                settings.elements.buttonContainer.html("").append(settings.elements.resetButton)
                settings.elements.scrRdr.html("Correct, click the reset button below to try again.")
                methods.bindEvents()
            }
            else if (correctCount != draggableIndex.length && settings.mode == "reset") {
                settings.elements.scrRdr.html("Incorrect, click the reset button below to try again.")
            }
            else {
                settings.elements.scrRdr.html("Some items are still incorrect, keep trying.")
            }

            $(settings.elements.main.find('.sortableInstructions')).focus()

            methods.addFeedback(feedback)
        },

        addFeedback : function(feedback) {
            if (feedback == true)
                settings.elements.feedbackContainer.html("<p><b>" + settings.feedback.correct + "</b></p>")
            else
                settings.elements.feedbackContainer.html("<p><b>" + settings.feedback.incorrect + "</b></p>")
        },

        changeScrReaderText : function(ctx, num) {
            var statics = settings.elements.main.find('.sortableStaticItem');
            var scrReaderTxt = ctx.text() + ", Match with: " + statics.eq(num).text()
            settings.elements.scrRdr.html(scrReaderTxt)
        },

        resetPlugin : function() {
            settings.elements.main.html("")
            methods.init()
        },

        adjustItemHeight : function() {

            var draggablesItems = settings.elements.main.find('.draggableObj li');
            var staticItems = settings.elements.main.find('.staticObj li');

            draggablesItems.each(function(i) {
                $(this).attr('data-item',i)
            });

            staticItems.each(function(i) {
                $(this).attr('data-item',i)
            });

            var all = [];

            draggablesItems.each(function(i) {
                all.push($(this))
            });

            staticItems.each(function(i) {
                all.push($(this))
            });

            var largest = 0;

            settings.elements.main.find('.draggableObj li').css('height', 'auto')
            settings.elements.main.find('.staticObj li').css('height', 'auto')

            $(all).each(function(i) {
                if ($(this)[0].scrollHeight > largest) {
                    largest = $(this)[0].scrollHeight
                }
            })

            settings.elements.main.find('.draggableObj li').css('height', largest)
            settings.elements.main.find('.staticObj li').css('height', largest)
        },

        getLargestHeight : function(item1,item2) {
            if (item1[0].clientHeight > item2[0].clientHeight)
                return item1[0].clientHeight
            else
                return item2[0].clientHeight
        },

        shuffle: function(arr) {
            for ( var j, x, i = arr.length; i; j = parseInt(Math.random() * i), x = arr[--i], arr[i] = arr[j], arr[j] = x);
            return arr;
        }
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        $(this).addClass('col-md-12')
        methods.init(this)
    });
};
