jQuery.fn.quiz = function(options) {

    var defaults = {
        // Defining default settings for quiz
        selectedAnswerArr: [],
        selectedCorrectArr: [],
        correctAnswerArr: [],
        icon: "tqi-activity",
        title: "Activity",
        instructions: "Please complete the question(s) below.",
        advanceOnIncorrect: true,
        displayCorrectAnswerInFeedback: true,
        currentQuestion: 1,
        correctCounter: 0,
        partialCounter: 0.0,
        showBackButton: false,
        showReviewButton: true,
        finishMessageTitle: "You\'re finished!",
        nextQuestionFeedback: "Click on the 'Next' button to start the next question.",
        finalQuestionFeedback: "Click on the 'Next' button to finish the quiz.",
    };

    var settings = $.extend( {}, defaults, options )

    var methods = {
        init: function() {
            // Calling Methods
            methods.createReusableElements()
            methods.createHeaderElements()
            methods.createBodyElements()
            methods.createHeader()
            methods.createBody()
            methods.createQuestion()
            methods.updateProgressBar()
            methods.bindEvents()
        },

        createReusableElements: function() {
            settings.elements.clearfix = $('<div class="clearfix"></div>') //Creating reusable clearfix div
        },

        createHeaderElements: function() {
            settings.elements.headerTitleIconWrapper = $('<div class="header-col-wrap col-xs-12 col-lg-8 col-md-8 col-sm-6">')
            settings.elements.headerIcon = $('<i aria-hidden="true" class="tqi ' + settings.icon + ' tqi-2x tq-panel-icon pull-left"></i>') // Create TQ Icon
            settings.elements.headerTitle = $('<h3>' + settings.title + '</h3>') // Create H3 Element
            settings.elements.headerInstructions = $('<p>' + settings.instructions + '</p>') // Create Instructions Element

            settings.elements.headerProgressColWrapper = $('<div class="header-col-wrap col-xs-12 col-lg-4 col-md-4 col-sm-6">')
            settings.elements.headerProgressBarWrapper = $('<div class="quiz-header-progress-bar progress">')
            settings.elements.headerProgressBar = $('<div class="progress-bar progress-bar-success" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100" style="width: 0%">')
            settings.elements.headerProgressText = $('<span class="quiz-progress-bar">40%</span>')
            settings.elements.headerProgressBarSr = $('<span class="sr-only">0% Complete (success)</span>')

            if (settings.insidePanel) {
                settings.elements.header = $('<div class="quiz-header-ip">') // Create Header Element
                settings.elements.headerCounter = $('<div class="quiz-header-counter-ip hidden-xs"></div>') // Create Counter Element
            }
            else {
                settings.elements.header = $('<div class="quiz-header">') // Create Header Element
                settings.elements.headerCounter = $('<span class="quiz-header-progress-counter">') // Create Counter Element
            }
        },

        createBodyElements: function() {
            settings.elements.bodyQuestionWrapper = $('<div class="quiz-body-question-wrapper"></div>') // create question wrapper
            settings.elements.bodyFeedback = $('<div class="quiz-body-feedback-wrapper"></div>') // Create body feedback wrapper
            settings.elements.bodyCorrectFeedback = $('<div class="alert alert-success" role="alert"></div>')
            settings.elements.bodyIncorrectFeedback = $('<div class="alert alert-danger" role="alert"></div>')
            settings.elements.bodyInfoFeedback = $('<div class="alert alert-info" role="alert"></div>')
            settings.elements.bodyAlertFeedback = $('<div class="alert alert-warning" role="alert"></div>')
            settings.elements.bodyCheckBtn = $('<button class="col-md-4 col-lg-4 col-sm-6 col-xs-12 pull-right quiz-check-btn btn btn-default">Check Answer <i class="fa fa fa-question-circle"></i></button>') // Create check answer button
            settings.elements.bodyResetBtn = $('<button class="col-md-2 col-lg-2 col-sm-12 col-xs-12 pull-right quiz-reset-btn btn btn-default">Reset <i class="fa fa-refresh"></i></button>') // Create reset answer button
            settings.elements.bodyReviewBtn = $('<button class="col-md-2 col-lg-2 col-sm-12 col-xs-12 pull-left quiz-review-btn btn btn-default">Review <i class="fa fa-paperclip"></i></button>')
            settings.elements.bodyBtnGroup = $('<div class="col-md-4 col-lg-4 col-sm-6 col-xs-12 pull-right btn-group body-next-prev-wrapper" role="group"></div>') // Create button group for prev/next buttons
            if (settings.showBackButton) {
                settings.elements.bodyPrevBtn = $('<button type="button" class="quiz-prev-button btn btn-default body-next-prev-duo"><i class="fa fa-chevron-left"></i> Prev</button>') // create prev btn
                settings.elements.bodyNextBtn = $('<button type="button" class="quiz-next-button btn btn-default body-next-prev-duo">Next <i class="fa fa-chevron-right"></i></button>') // create next btn
            }
            else {
                settings.elements.bodyPrevBtn = $('<button type="button" class="quiz-prev-button btn btn-default"><i class="fa fa-chevron-left"></i> Prev</button>') // create prev btn
                settings.elements.bodyNextBtn = $('<button type="button" class="quiz-next-button btn btn-default body-next-prev-solo">Next <i class="fa fa-chevron-right"></i></button>') // create next btn
            }

            if (settings.insidePanel) {
                settings.elements.body = $('<div class="quiz-body-ip">') // Create Body Element
            }
            else {
                settings.elements.body = $('<div class="quiz-body">') // Create Body Element
            }
        },

        createHeader: function() {
            settings.elements.main.append(settings.elements.header) // Append Header
            if (!settings.insidePanel) {
                settings.elements.header.append(settings.elements.headerTitleIconWrapper)
                settings.elements.headerTitleIconWrapper.append(settings.elements.headerIcon) // Apend TQ Icon
                settings.elements.headerTitleIconWrapper.append(settings.elements.headerTitle) // Append H3
                settings.elements.header.append(settings.elements.headerProgressColWrapper)
                settings.elements.headerProgressColWrapper.append(methods.createProgressBar())
                settings.elements.header.append(settings.elements.clearfix) // Apending Clearfix
                settings.elements.header.append(settings.elements.headerInstructions) // Append Instructions
            }
            else {
                settings.elements.header.append(settings.elements.headerProgressColWrapper)
                settings.elements.headerProgressColWrapper.append(methods.createProgressBar())
                settings.elements.header.append(settings.elements.clearfix) // Apending Clearfix
                settings.elements.header.append(settings.elements.headerInstructions) // Append Instructions
            }
        },

        createProgressBar: function() {
            settings.elements.headerProgressBarWrapper.append(settings.elements.headerProgressBar)
            settings.elements.headerProgressBar.append(settings.elements.headerCounter)
            settings.elements.headerProgressBar.append(settings.elements.headerProgressBarSr)
            return(settings.elements.headerProgressBarWrapper)
        },

        createBody: function() {
            settings.elements.main.append(settings.elements.body) // Append Body
            settings.elements.body.append(settings.elements.bodyQuestionWrapper) // append question wrapper
            settings.elements.body.append(settings.elements.bodyFeedback) // append feedback wrapper
            settings.elements.body.append(settings.elements.bodyCheckBtn) // Append check answer button
            settings.elements.body.append(settings.elements.bodyBtnGroup.hide()) // append button container (group)
            if (settings.showBackButton)
                settings.elements.bodyBtnGroup.append(settings.elements.bodyPrevBtn) // append prev button to button group
            settings.elements.bodyBtnGroup.append(settings.elements.bodyNextBtn) // append next button to button group
            settings.elements.body.append(settings.elements.clearfix.clone()) // Apending Clearfix

        },

        createQuestion: function() {
            settings.elements.main.find(".quiz-body-feedback-wrapper").removeAttr("tabindex")
            settings.elements.main.find(".quiz-body-question-wrapper").html("") // Removing previous question for current question
            settings.elements.bodyQuestionWrapper.append($('<p class="quiz-body-question" tabindex="0">' + methods.createQuestionInstructions() + '</p>')) // Creating new question instructions
            settings.elements.bodyQuestionWrapper.append($('' + methods.createQuestions() + '</div>')) // Creating new question
        },

        updateProgressBar: function() {
            var math = settings.currentQuestion/settings.questions.length * 100
            settings.elements.headerProgressBarSr.html(math + "% complete")
            settings.elements.headerProgressBar.attr("aria-valuenow", math)
            settings.elements.headerProgressBar.css("width", "" + math + "%")
            settings.elements.headerCounter.html('<b>'+ settings.currentQuestion + '</b> / ' + settings.questions.length + '')
        },

        createQuestionInstructions: function() {
            return(settings.questions[settings.currentQuestion - 1].question) // Gets current question instructions based on currentQuestion variable
        },

        createQuestions: function() {
            if (settings.questions[settings.currentQuestion - 1].type == "multi") {
                return(methods.createMulti()) // If current question type is multi call createMulti() method
            }
            if (settings.questions[settings.currentQuestion - 1].type == "tick") {
                return(methods.createTick()) // If current question type is tick call createTick() method
            }
            if (settings.questions[settings.currentQuestion - 1].type == "textentry") {
                return(methods.createText()) // If current question type is textentry call createText() method
            }
            if (settings.questions[settings.currentQuestion - 1].type == "cloze") {
                return(methods.createCloze()) // If current question type is cloze call createCloze() method
            }
            if (settings.questions[settings.currentQuestion - 1].type == "matching") {
                return(methods.createMatching()) // If current question type is cloze call createCloze() method
            }
        },

        createMulti: function() {
            var htmlArr = [];
            var current = settings.questions[settings.currentQuestion - 1];
            htmlArr.push('<fieldset><legend></legend>')

            for (var i = 0; i < current.answers.length; i++) {
                if (current.answers[i].correct) {
                    htmlArr.push('<label class="quiz-body-multi-label">')
                    htmlArr.push('<input data-correct="true" type="radio" name="'+settings.elements.main.attr('id')+'-radio"> ' + current.answers[i].answer + '')
                    htmlArr.push('</label>')
                }
                else {
                    htmlArr.push('<label class="quiz-body-multi-label">')
                    htmlArr.push('<input type="radio" name="'+settings.elements.main.attr('id')+'-radio"> ' + current.answers[i].answer + '')
                    htmlArr.push('</label>')
                }
            }

            htmlArr.push('</fieldset>')

            return(htmlArr.join(" "))
        },

        createTick: function() {
            var htmlArr = [];
            var current = settings.questions[settings.currentQuestion - 1];
            htmlArr.push('<fieldset><legend></legend>')

            for (var i = 0; i < current.answers.length; i++) {
                if (current.answers[i].correct) {
                    htmlArr.push('<label class="quiz-body-tick-label">')
                    htmlArr.push('<input data-correct="true" type="checkbox" name="'+settings.elements.main.attr('id')+'-tick"> ' + current.answers[i].answer + '')
                    htmlArr.push('</label>')
                }
                else {
                    htmlArr.push('<label class="quiz-body-tick-label">')
                    htmlArr.push('<input type="checkbox" name="'+settings.elements.main.attr('id')+'-tick"> ' + current.answers[i].answer + '')
                    htmlArr.push('</label>')
                }
            }

            htmlArr.push('</fieldset>')
            return(htmlArr.join(" "))
        },

        createText: function() {
            var htmlArr = [];

            htmlArr.push('<fieldset>')
            htmlArr.push('<legend></legend>')
            htmlArr.push('<label class="quiz-body-text-label">')
            htmlArr.push('<p class="sr-only">Please enter your answer in the text area below.</p>')
            htmlArr.push('<textarea></textarea>')
            htmlArr.push('</label>')
            htmlArr.push('</fieldset>')
            htmlArr.push('<div class="quiz-body-text-alert alert alert-warning" role="alert"><i class="fa fa-2x fa-sticky-note"></i><h3>Note</h3><div class="clearfix"></div><p>This question type is not marked and will not appear in the final results. Please compare your answer to the model response provided after clicking the \'Check Answer\' button.</p></div>')

            return(htmlArr.join(" "))
        },

        createCloze: function() {
            var htmlArr = [];
            var current = settings.questions[settings.currentQuestion - 1];
            var clozeQuestion = methods.getClozeAnswers(current.answers[0].answer)

            htmlArr.push('<fieldset><legend></legend>')
            htmlArr.push(clozeQuestion)
            htmlArr.push('</fieldset>')

            return(htmlArr.join(" "))
        },

        createMatching: function() {
            var htmlArr = [];
            var current = settings.questions[settings.currentQuestion - 1];
            var matchingQuestion = methods.buildMatching(current.answers)
            htmlArr.push('<fieldset><legend></legend>')
            if (current.headings != undefined) {
                htmlArr.push('<div class="quiz-matching-headings hidden-sm hidden-xs">')
                htmlArr.push('<p class="pull-left"><b>' + current.headings.left + '</b></p>')
                htmlArr.push('<p class="pull-right"><b>' + current.headings.right + '</b></p>')
                htmlArr.push('<div class="clearfix"></div></div>')
            }
            htmlArr.push(matchingQuestion)
            htmlArr.push('</fieldset>')
            return(htmlArr.join(" "))
        },

        buildMatching: function(answer) {
            var html = [];
            for (var i = 0; i < answer.length; i++) {
                var select = methods.getMatchingSelect(answer, i)
                html.push('<div class="quiz-matching-question-wrapper">')
                html.push('<div class="row">')
                html.push('<div class="col-xs-12 col-md-9">' + answer[i].question + '</div>')
                html.push('<div class="quiz-matching-select col-xs-12 col-md-3">' + select + '</div>')
                html.push('<div class="clearfix"></div>')
                html.push('</div>')
                html.push('</div>')
            }
            return(html)
        },

        getMatchingSelect: function(answer, correct) {
            var htmlArr = [];
            for (var i = 0; i < answer.length; i++) {
                if (i == correct) {
                    htmlArr.push('<option data-correct="true">' + answer[i].answer + '</option>')
                }
                else {
                    htmlArr.push('<option>' + answer[i].answer + '</option>')
                }
            }
            methods.shuffle(htmlArr)
            htmlArr.unshift('<option>' + "-- Select --" + '</option>')
            htmlArr.unshift('<label><select name="cloze">')
            htmlArr.push('</select></label>')
            return(htmlArr)
        },

        getClozeAnswers: function(answer) {
            var str = answer;
            var arrayOpen = '[';
            var arrayClose = ']';
            var arr = [];

            $.each(str.split(''),function(i,v){
                if(v == arrayOpen) arr.push(i+1);
                if(v == arrayClose) arr.push(i);
            });

            var newArr = [];

            for (var i = 0; i < arr.length; i+=2) {
                newArr.push(str.substring(arr[i],arr[i+1]))
            }

            var typeArr = [];
            var singleArr = [];
            var multiArr = [];

            for (var i = 0; i < newArr.length; i++) {
                $.each(newArr[i].split(''),function(x,v){
                    if (v == ",") {
                        typeArr.push("multi")
                        multiArr.push(methods.splitMulti(newArr[i]))
                        return false;
                    }
                    else if (newArr[i].charAt(x+1) == "") {
                        typeArr.push("single")
                        singleArr.push(newArr[i])
                    }
                });
            }

            return(methods.buildCloze(str, arr, typeArr, singleArr, multiArr))
        },

        splitMulti: function(arr) {
            var splitArr = [];

            var str = arr;
            var strArr = str.split(',');

            for(var i = 0; i < strArr.length; i++) {
               strArr[i] = strArr[i].replace(/^\s*/, "").replace(/\s*$/, "");
            }

            return(strArr)
        },

        buildCloze: function(str, arr, typeArr, singleArr, multiArr) {
            if (multiArr.length > 0) {
                var multiAnswer = methods.buildMultiCloze(multiArr)
            }
            if (singleArr.length > 0) {
                var singleAnswer = methods.buildSingleCloze(singleArr)
            }

            var finalStr = methods.insertClozeAnswers(multiAnswer,singleAnswer,str,arr,typeArr);

            return(finalStr)
        },

        buildMultiCloze: function(multiArr) {
            var htmlArr = [];

            $(multiArr).each(function(i) {
                var arr = [];

                for (var x = 0; x < $(this).length; x++) {
                    var str = multiArr[i][x];
                    if (str.indexOf("*") == -1) {
                        arr.push('<option>' + str + '</option>')
                    }
                    else {
                        arr.push('<option data-correct="true">' + str.replace(/\*/g, '') + '</option>')
                    }
                }
                methods.shuffle(arr)
                arr.unshift('<option>' + "-- Select --" + '</option>')
                arr.unshift('<label><select name="cloze">')
                arr.push('</select></label>')
                htmlArr.push(arr)
            })

            return(htmlArr)
        },

        buildSingleCloze: function(singleArr) {
            var htmlArr = [];

            for (var i = 0; i < singleArr.length; i++) {
                htmlArr.push('<option data-correct="'+i+'">' + singleArr[i] + '</option>')
            }

            methods.shuffle(htmlArr)
            htmlArr.unshift('<option>' + "-- Select --" + '</option>')
            htmlArr.unshift('<label><select class="single-select" name="cloze">')
            htmlArr.push('</select></label>')

            return(htmlArr)
        },

        insertClozeAnswers: function(multi,single,str,arr,typeArr) {
            var newStr = [];
            var startArr = [];
            var endArr = [];

            for (var i = 0; i < arr.length; i++) {
                if (i % 2 == 0) {
                    startArr.push(arr[i])
                }
                else {
                    endArr.push(arr[i])
                }
            }

            var multiCount = 0;
            for (var i = 0; i < typeArr.length; i++) {
                if (typeArr[i] == "single") {
                    newStr.push(single)
                }
                else if (typeArr[i] == "multi") {
                    newStr.push(multi[multiCount])
                    multiCount++;
                }
            }

            var finalStr = [];

            var count = 0;

            for (var i = 0; i < arr.length/2; i++) {
                if (i == 0) {
                    finalStr.push(str.substring(0,startArr[i]-1))
                    finalStr.push(newStr[0])
                }
                else {
                    finalStr.push(str.substring(endArr[i-1]+1,startArr[i]-1))
                    finalStr.push(newStr[i])
                }
                if (i+1 >= (arr.length/2)) {
                    finalStr.push(str.substring(endArr[i]+1))
                }
            }

            return(finalStr.join(" "))
        },

        bindEvents: function() {
            settings.elements.bodyCheckBtn.on("click", function() {
                methods.checkAnswer()
            })
            settings.elements.bodyNextBtn.on("click", function() {
                methods.nextQuestion()
            })
            settings.elements.bodyPrevBtn.on("click", function() {
                methods.prevQuestion()
            })
            settings.elements.bodyResetBtn.on("click", function() {
                methods.reset()
            })
            settings.elements.bodyReviewBtn.on("click", function() {
                methods.review()
            })
        },

        checkAnswer: function() {
            if (settings.questions[settings.currentQuestion - 1].type == "multi") {
                methods.checkMulti()
            }
            if (settings.questions[settings.currentQuestion - 1].type == "tick") {
                methods.checkTick()
            }
            if (settings.questions[settings.currentQuestion - 1].type == "textentry") {
                methods.checkText()
            }
            if (settings.questions[settings.currentQuestion - 1].type == "cloze" || settings.questions[settings.currentQuestion - 1].type == "matching") {
                methods.checkCloze()
            }
        },

        checkMulti: function() {
            var current = settings.questions[settings.currentQuestion - 1];

            settings.elements.main.find(".quiz-body-multi-label").each(function(i) {
                if ($(this).children().is(':checked') && $(this).children().data("correct")) {
                    settings.selectedAnswerArr.push("<p>" + $(this).text().trim() + "</p>")
                    settings.selectedCorrectArr.push("<p>" + $(this).text().trim() + "</p>")
                    settings.correctCounter++
                    methods.appendCorrectFeedback($(this).index())
                    settings.elements.main.find(".quiz-check-btn").hide()
                    settings.elements.bodyBtnGroup.show()
                    $(this).parent().find("input").attr("disabled", true)
                    return false
                }
                else if ($(this).children().is(':checked') && !$(this).children().data("correct")) {
                    settings.selectedAnswerArr.push("<p>" + $(this).text().trim() + "</p>")
                    settings.selectedCorrectArr.push("<p>" + $($(this).parent().find("input[data-correct=true]").parent()).text() + "</p>")
                    methods.appendIncorrectFeedback($(this).index())

                    if (settings.advanceOnIncorrect) {
                        $(this).parent().find("input").attr("disabled", true)
                        settings.elements.main.find(".quiz-check-btn").hide()
                        settings.elements.bodyBtnGroup.show()
                    }

                    return false
                }
                else {
                    methods.appendAlertFeedback()
                }
            })
        },

        checkTick: function() {
            var current = settings.questions[settings.currentQuestion - 1];
            var correctTotal = 0;
            var correctCount = 0;
            var selectedCount = 0;
            var selected = [];
            var correctAnswers = [];
            for (var i = 0; i < current.answers.length; i++) {
                if (current.answers[i].correct) {
                    correctAnswers.push("<p>" + current.answers[i].answer + "</p>")
                    correctTotal++
                }
            }

            settings.elements.main.find(".quiz-body-tick-label").each(function(i) {
                if ($(this).children().is(':checked') && $(this).children().data("correct")) {
                    selected.push("<p>" + $(this).text().trim() + "</p>")
                    correctCount++
                    selectedCount++
                }
                else if ($(this).children().is(':checked') && !$(this).children().data("correct")) {
                    selected.push("<p>" + $(this).text().trim() + "</p>")
                    selectedCount++
                }
            })

            if (correctCount == correctTotal && correctCount == selectedCount) {
                settings.selectedAnswerArr.push(selected);
                settings.selectedCorrectArr.push(correctAnswers.join(""));
                settings.correctCounter++
                methods.appendCorrectFeedback()
                settings.elements.main.find(".quiz-check-btn").hide()
                settings.elements.bodyBtnGroup.show()
                settings.elements.main.find(".quiz-body-tick-label").children().attr("disabled", true)
            }
            else if (correctCount != correctTotal && selectedCount > 0 || correctCount == correctTotal && selectedCount > correctCount) {
                settings.selectedAnswerArr.push(selected);
                settings.selectedCorrectArr.push(correctAnswers.join(""));
                var num = correctCount / correctTotal;
                settings.partialCounter += Math.ceil(num * 10) / 10;
                methods.appendIncorrectFeedback()

                if (settings.advanceOnIncorrect) {
                    settings.elements.main.find(".quiz-check-btn").hide()
                    settings.elements.bodyBtnGroup.show()
                    settings.elements.main.find(".quiz-body-tick-label").children().attr("disabled", true)
                }
            }
            else {
                methods.appendAlertFeedback()
            }
        },

        checkText: function() {
            if (settings.elements.main.find(".quiz-body-text-label").find("textarea").val().length == 0) {
                methods.appendAlertFeedback(true)
            }
            else {
                methods.appendTextentryFeedback()
                settings.selectedAnswerArr.push("<p>" + settings.elements.main.find(".quiz-body-text-label").find("textarea").val() + "</p>");
                settings.selectedCorrectArr.push("<p>" + settings.questions[settings.currentQuestion - 1].answers[0].answer + "</p>");
                settings.correctAnswerArr.push("<p>" + settings.questions[settings.currentQuestion - 1].answers[0].answer + "</p>")
                settings.elements.main.find(".quiz-check-btn").hide()
                settings.elements.bodyBtnGroup.show()
                settings.elements.main.find("textarea").attr("disabled", true)
            }
        },

        checkCloze: function() {
            var current = settings.questions[settings.currentQuestion - 1];
            var correct = false;
            var none = true;
            var selected = [];
            var correct = [];
            settings.elements.main.find(".quiz-body-question-wrapper").find("select").each(function(i) {
                if ($(this).find(":selected")[0].value == "-- Select --") {
                    $(this).addClass("quiz-unselected")
                }
                else {
                    $(this).removeClass("quiz-unselected")
                }
            })

            if (settings.elements.main.find(".quiz-unselected").length == 0) {
                none = false;
            }

            if (!none) {
                var singleCount = 0;
                var correctCount = 0;
                var correctTotal = settings.elements.main.find(".quiz-body-question-wrapper").find("select").length
                settings.elements.main.find(".quiz-body-question-wrapper").find("select").each(function(i) {
                    if ($(this).hasClass("single-select")) {
                        if ($(this).find(":selected").data("correct") == singleCount) {
                            correct = true;
                            correctCount++;
                        }
                        else {
                            correct = false;
                            return false;
                        }
                        singleCount++;
                    }
                    else if ($(this).find(":selected").data("correct")) {
                        correct = true;
                        correctCount++;
                    }
                    else {
                        correct = false;
                        return false;
                    }
                })
            }

            selected = [];
            correctArr = [];
            var count = 0;
            settings.elements.main.find("select").each(function(i) {
                selected.push("<p><b>"+(i+1)+":</b> " + $(this).val() + "</p>");
                if ($(this).hasClass("single-select")) {
                    correctArr.push("<p><b>"+(i+1)+":</b> " + $(this).find("option[data-correct="+count+"]").val() + "</p>")
                    count++;
                }
                else {
                    correctArr.push("<p><b>"+(i+1)+":</b> " + $(this).find("option[data-correct=true]").val() + "</p>")
                }
            })

            if (none) {
                methods.appendAlertFeedback()
            }
            else if (correct) {
                settings.selectedAnswerArr.push(selected)
                settings.selectedCorrectArr.push(correctArr.join(""))
                settings.correctCounter++
                methods.appendCorrectFeedback()
                settings.elements.main.find(".quiz-check-btn").hide()
                settings.elements.bodyBtnGroup.show()
                settings.elements.main.find(".quiz-body-question-wrapper").find("select").attr("disabled", true)
            }
            else {
                settings.selectedAnswerArr.push(selected)
                settings.selectedCorrectArr.push("Incorrect")
                var num = correctCount / correctTotal;
                settings.partialCounter += Math.ceil(num * 10) / 10;
                methods.appendIncorrectFeedback()

                if (settings.advanceOnIncorrect) {
                    settings.elements.main.find(".quiz-body-question-wrapper").find("select").attr("disabled", true)
                    settings.elements.main.find(".quiz-check-btn").hide()
                    settings.elements.bodyBtnGroup.show()
                }
            }
        },

        appendCorrectFeedback: function(correctIndex) {
            settings.elements.main.find(".quiz-body-question").removeAttr("tabindex")
            settings.elements.main.find(".quiz-body-feedback-wrapper").attr("tabindex", "0")
            var current = settings.questions[settings.currentQuestion - 1];

            settings.elements.bodyFeedback.html("")
            settings.elements.bodyCorrectFeedback.html("").hide()

            if (current.type == "multi" && current.answers[correctIndex - 1].feedback != undefined) {
                settings.elements.bodyFeedback.append(settings.elements.bodyCorrectFeedback)
                settings.elements.bodyCorrectFeedback.append('<i class="fa fa-2x fa-check-circle"></i><h3>Correct</h3><div class="clearfix"></div>')
                if (settings.displayCorrectAnswerInFeedback) {
                    settings.elements.bodyCorrectFeedback.append(methods.injectAnswer())
                }
                settings.elements.bodyCorrectFeedback.append('<p>' + current.answers[correctIndex - 1].feedback + '</p>')
                if (settings.currentQuestion < settings.questions.length) {
                    settings.elements.bodyCorrectFeedback.append('<hr/><p class="small">' + settings.nextQuestionFeedback + '</p>')
                }
                else {
                    settings.elements.bodyCorrectFeedback.append('<hr/><p class="small">' + settings.finalQuestionFeedback + '</p>')
                }
            }
            else if (current.feedback != undefined && current.feedback.correct != undefined && current.feedback.correct.length != 0) {
                settings.elements.bodyFeedback.append(settings.elements.bodyCorrectFeedback)
                settings.elements.bodyCorrectFeedback.append('<i class="fa fa-2x fa-check-circle"></i><h3>Correct</h3><div class="clearfix"></div>')
                if (settings.displayCorrectAnswerInFeedback) {
                    settings.elements.bodyCorrectFeedback.append(methods.injectAnswer())
                }
                settings.elements.bodyCorrectFeedback.append('<p>' + current.feedback.correct + '</p>')
                if (settings.currentQuestion < settings.questions.length) {
                    settings.elements.bodyCorrectFeedback.append('<hr/><p class="small">' + settings.nextQuestionFeedback + '</p>')
                }
                else {
                    settings.elements.bodyCorrectFeedback.append('<hr/><p class="small">' + settings.finalQuestionFeedback + '</p>')
                }
            }
            else if (current.feedback != undefined && current.feedback.generic != undefined && current.feedback.generic.length != 0) {
                methods.appendGenericFeedback(true)
            }
            else {
                settings.elements.bodyFeedback.append(settings.elements.bodyCorrectFeedback)
                settings.elements.bodyCorrectFeedback.append('<i class="fa fa-2x fa-check-circle"></i><h3>Correct</h3><div class="clearfix"></div>')
                if (settings.displayCorrectAnswerInFeedback) {
                    settings.elements.bodyCorrectFeedback.append(methods.injectAnswer())
                }
                if (settings.currentQuestion < settings.questions.length) {
                    settings.elements.bodyCorrectFeedback.append('<hr/><p class="small">' + settings.nextQuestionFeedback + '</p>')
                }
                else {
                    settings.elements.bodyCorrectFeedback.append('<hr/><p class="small">' + settings.finalQuestionFeedback + '</p>')
                }
            }

            settings.elements.bodyCorrectFeedback.fadeIn({queue:false, duration:300})
        },

        appendIncorrectFeedback: function(incorrectIndex) {
            settings.elements.main.find(".quiz-body-question").removeAttr("tabindex")
            settings.elements.main.find(".quiz-body-feedback-wrapper").attr("tabindex", "0")
            var current = settings.questions[settings.currentQuestion - 1];

            settings.elements.bodyFeedback.html("")
            settings.elements.bodyIncorrectFeedback.html("").hide()

            if (current.type == "multi" && current.answers[incorrectIndex - 1].feedback != undefined) {
                settings.elements.bodyFeedback.append(settings.elements.bodyIncorrectFeedback)

                if (settings.advanceOnIncorrect) {
                    settings.elements.bodyIncorrectFeedback.append('<i class="fa fa-2x fa-times-circle"></i><h3>Incorrect</h3><div class="clearfix">')
                    if (settings.displayCorrectAnswerInFeedback) {
                        settings.elements.bodyIncorrectFeedback.append(methods.injectAnswer())
                    }
                    settings.elements.bodyIncorrectFeedback.append('<p>' + current.answers[incorrectIndex - 1].feedback + '</p>')
                    if (settings.currentQuestion < settings.questions.length) {
                        settings.elements.bodyIncorrectFeedback.append('<hr/><p class="small">' + settings.nextQuestionFeedback + '</p>')
                    }
                    else {
                        settings.elements.bodyIncorrectFeedback.append('<hr/><p class="small">' + settings.finalQuestionFeedback + '</p>')
                    }
                }
                else {
                    settings.elements.bodyIncorrectFeedback.append('<i class="fa fa-2x fa-times-circle"></i><h3>Incorrect - Try Again</h3><div class="clearfix">')
                    if (settings.displayCorrectAnswerInFeedback) {
                        settings.elements.bodyIncorrectFeedback.append(methods.injectAnswer())
                    }
                    settings.elements.bodyIncorrectFeedback.append('<p>' + current.feedback.incorrect + '</p>')
                }
            }
            else if (current.feedback != undefined && current.feedback.incorrect != undefined && current.feedback.incorrect.length != 0) {
                settings.elements.bodyFeedback.append(settings.elements.bodyIncorrectFeedback)

                if (settings.advanceOnIncorrect) {
                    settings.elements.bodyIncorrectFeedback.append('<i class="fa fa-2x fa-times-circle"></i><h3>Incorrect</h3><div class="clearfix">')
                    if (settings.displayCorrectAnswerInFeedback) {
                        settings.elements.bodyIncorrectFeedback.append(methods.injectAnswer())
                    }
                    settings.elements.bodyIncorrectFeedback.append('<p>' + current.feedback.incorrect + '</p>')
                    if (settings.currentQuestion < settings.questions.length) {
                        settings.elements.bodyIncorrectFeedback.append('<hr/><p class="small">' + settings.nextQuestionFeedback + '</p>')
                    }
                    else {
                        settings.elements.bodyIncorrectFeedback.append('<hr/><p class="small">' + settings.finalQuestionFeedback + '</p>')
                    }
                }
                else {
                    settings.elements.bodyIncorrectFeedback.append('<i class="fa fa-2x fa-times-circle"></i><h3>Incorrect - Try Again</h3><div class="clearfix">')
                    if (settings.displayCorrectAnswerInFeedback) {
                        settings.elements.bodyIncorrectFeedback.append(methods.injectAnswer())
                    }
                    settings.elements.bodyIncorrectFeedback.append('<p>' + current.feedback.incorrect + '</p>')
                }
            }
            else if (current.feedback != undefined && current.feedback.generic != undefined && current.feedback.generic.length != 0) {
                methods.appendGenericFeedback(false)
            }
            else {
                settings.elements.bodyFeedback.append(settings.elements.bodyIncorrectFeedback)

                if (settings.advanceOnIncorrect) {
                    settings.elements.bodyIncorrectFeedback.append('<i class="fa fa-2x fa-times-circle"></i><h3>Incorrect</h3><div class="clearfix">')
                    if (settings.displayCorrectAnswerInFeedback) {
                        settings.elements.bodyIncorrectFeedback.append(methods.injectAnswer())
                    }
                    if (settings.currentQuestion < settings.questions.length) {
                        settings.elements.bodyIncorrectFeedback.append('<hr/><p class="small">' + settings.nextQuestionFeedback + '</p>')
                    }
                    else {
                        settings.elements.bodyIncorrectFeedback.append('<hr/><p class="small">' + settings.finalQuestionFeedback + '</p>')
                    }
                }
                else {
                    settings.elements.bodyIncorrectFeedback.append('<i class="fa fa-2x fa-times-circle"></i><h3>Incorrect - Try Again</h3><div class="clearfix">')
                    if (settings.displayCorrectAnswerInFeedback) {
                        settings.elements.bodyIncorrectFeedback.append(methods.injectAnswer())
                    }
                }
            }
            settings.elements.bodyIncorrectFeedback.fadeIn({queue:false, duration:300})
        },

        appendGenericFeedback: function(bool) {
            settings.elements.main.find(".quiz-body-question").removeAttr("tabindex")
            settings.elements.main.find(".quiz-body-feedback-wrapper").attr("tabindex", "0")
            var current = settings.questions[settings.currentQuestion - 1];
            settings.elements.bodyFeedback.html("")
            settings.elements.bodyInfoFeedback.html("").removeClass("quiz-body-final-feedback").hide()

            if (bool) {
                settings.elements.bodyFeedback.append(settings.elements.bodyCorrectFeedback)
                settings.elements.bodyCorrectFeedback.append('<i class="fa fa-2x fa-check-circle"></i><h3>Correct</h3><div class="clearfix"></div>')
                if (settings.displayCorrectAnswerInFeedback) {
                    settings.elements.bodyCorrectFeedback.append(methods.injectAnswer())
                }
            }
            else {
                settings.elements.bodyFeedback.append(settings.elements.bodyIncorrectFeedback)
                settings.elements.bodyIncorrectFeedback.append('<i class="fa fa-2x fa-times-circle"></i><h3>Incorrect</h3><div class="clearfix">')
                if (settings.displayCorrectAnswerInFeedback) {
                    settings.elements.bodyIncorrectFeedback.append(methods.injectAnswer())
                }
            }

            settings.elements.bodyFeedback.append(settings.elements.bodyInfoFeedback)
            settings.elements.bodyInfoFeedback.append('<i class="fa fa-2x fa-comment"></i><h3>Feedback</h3><div class="clearfix"></div>')

            settings.elements.bodyInfoFeedback.append('<p>' + current.feedback.generic + '</p>')
            if (settings.currentQuestion < settings.questions.length) {
                settings.elements.bodyInfoFeedback.append('<p class="small">' + settings.nextQuestionFeedback + '</p>')
            }
            else {
                settings.elements.bodyInfoFeedback.append('<p class="small">' + settings.finalQuestionFeedback + '</p>')
            }
            settings.elements.bodyInfoFeedback.fadeIn({queue:false, duration:300})
        },

        appendAlertFeedback: function(bool) {
            settings.elements.bodyFeedback.html("").hide()
            if (bool) {
                settings.elements.bodyFeedback.append(settings.elements.bodyAlertFeedback.html('<i class="fa fa-2x fa-exclamation-circle"></i><h3>Alert</h3><div class="clearfix"></div><p>Please enter an answer</p>'))
            }
            else {
                settings.elements.bodyFeedback.append(settings.elements.bodyAlertFeedback.html('<i class="fa fa-2x fa-exclamation-circle"></i><h3>Alert</h3><div class="clearfix"></div><p>Please select an answer</p>'))
            }
            settings.elements.bodyFeedback.fadeIn({queue:false, duration:300})
        },

        injectAnswer: function() {
            if (settings.questions[settings.currentQuestion-1].type == "multi" || settings.questions[settings.currentQuestion-1].type == "tick") {
                var arr = [];
                var correctAnswers = [];
                var str = "";

                for (var i = 0; i < settings.questions[settings.currentQuestion-1].answers.length; i++) {
                    if (settings.questions[settings.currentQuestion-1].answers[i].correct) {
                        correctAnswers.push("<p>" + settings.questions[settings.currentQuestion-1].answers[i].answer + "</p>")
                        arr.push(settings.questions[settings.currentQuestion-1].answers[i].answer)
                    }
                }

                str = arr.toString();
                var finalStr = str.replace(/\,/g, ' and ');

                settings.correctAnswerArr.push(correctAnswers.join(""))
                return("<p>The answer was:</p><p>" + finalStr + "</p>")
            }
            else if (settings.questions[settings.currentQuestion-1].type == "cloze" || settings.questions[settings.currentQuestion-1].type == "matching") {
                var html = [];
                html.push("<table class='quiz-body-feedback-table top-buffer table table-bordered table-striped'><thead><tr><th>Selected</th><th>Correct</th></tr></thead><tbody>")
                var singleCount = 0;

                var correctAnswers = [];
                settings.elements.main.find("select").each(function(i) {
                    if ($(this).hasClass("single-select")) {
                        var correct = $(this).find("option[data-correct='"+singleCount+"']").val()
                        correctAnswers.push("<p><b>" + (i + 1) + ":</b> " + correct + "</p>")
                        singleCount++;
                    }
                    else {
                        var correct = $(this).find("option[data-correct='true']").val()
                        correctAnswers.push("<p><b>" + (i + 1) + ":</b> " + correct + "</p>")
                    }
                    var selected = $(this).val();
                    html.push("<tr>")
                    if (correct == selected) {
                        html.push("<td><span>"+selected+" <i class='fa fa-check fa-2x'></span></td>")
                    }
                    else {
                        html.push("<td>"+selected+" <i class='fa fa-times fa-2x'></td>")
                    }
                    html.push("<td>"+correct+"</td>")
                    html.push("</tr>")
                });
                settings.correctAnswerArr.push(correctAnswers.join(""))
                html.push("</tbody></table>")
                return(html.join(""))
            }
        },

        appendTextentryFeedback: function() {
            settings.elements.main.find(".quiz-body-question").removeAttr("tabindex")
            settings.elements.main.find(".quiz-body-feedback-wrapper").attr("tabindex", "0")
            var current = settings.questions[settings.currentQuestion - 1];

            settings.elements.bodyFeedback.html("")
            settings.elements.bodyInfoFeedback.html("").removeClass("quiz-body-final-feedback").hide()
            settings.elements.bodyFeedback.append(settings.elements.bodyInfoFeedback)
            settings.elements.bodyInfoFeedback.append('<i class="fa fa-2x fa-comment"></i><h3>Model response</h3><div class="clearfix"></div>')
            settings.elements.bodyInfoFeedback.append('<p>' + current.answers[0].answer + '</p>')
            if (current.feedback != undefined) {
                if (current.feedback.generic != undefined) {
                    settings.elements.bodyInfoFeedback.append('<hr/><p>' + current.feedback.generic + '</p>')
                }
            }
            if (settings.currentQuestion < settings.questions.length) {
                settings.elements.bodyInfoFeedback.append('<hr/><p class="small">' + settings.nextQuestionFeedback + '</p>')
            }
            else {
                settings.elements.bodyInfoFeedback.append('<p class="small">' + settings.finalQuestionFeedback + '</p>')
            }
            settings.elements.bodyInfoFeedback.fadeIn({queue:false, duration:300})
        },

        nextQuestion: function() {
            if (settings.currentQuestion != settings.questions.length) {
                settings.currentQuestion++
                settings.elements.bodyFeedback.html("")
                settings.elements.main.find(".quiz-check-btn").show()
                settings.elements.bodyBtnGroup.hide()
                methods.updateProgressBar()
                methods.createQuestion()
            }
            else {
                settings.elements.bodyFeedback.html("")
                settings.elements.bodyResetBtn.insertBefore(settings.elements.bodyCheckBtn)
                if (settings.showReviewButton)
                    settings.elements.bodyReviewBtn.insertBefore(settings.elements.bodyCheckBtn)
                settings.elements.bodyResetBtn.show()
                if (settings.showReviewButton)
                    settings.elements.bodyReviewBtn.show()
                settings.elements.bodyCheckBtn.hide()
                settings.elements.bodyBtnGroup.hide()
                methods.finalFeedback()
            }
        },

        prevQuestion: function() {
            settings.currentQuestion--
            settings.elements.bodyFeedback.html("")
            settings.elements.main.find(".quiz-check-btn").removeAttr("disabled")
            settings.elements.main.find(".quiz-next-button").attr("disabled", true)
            methods.updateProgressBar()
            methods.createQuestion()

            if (settings.currentQuestion == 1)
                settings.elements.main.find(".quiz-prev-button").attr("disabled", true)
        },

        finalFeedback: function() {
            var finalScore = settings.correctCounter + settings.partialCounter
            var finalQuestionCount = settings.questions.length;

            for (var i = 0; i < settings.questions.length; i++) {
                if (settings.questions[i].type == "textentry") {
                    if (!finalQuestionCount <= 0) {
                        finalQuestionCount--;
                    }
                }
            }

            settings.elements.bodyQuestionWrapper.html("").hide()
            settings.elements.bodyInfoFeedback.html("").hide()
            settings.elements.bodyFeedback.append(settings.elements.bodyInfoFeedback.addClass("quiz-body-final-feedback"))
            settings.elements.bodyInfoFeedback.append('<h3 class="text-center"><i class="fa fa-trophy"></i> '+ settings.finishMessageTitle +'</h3><div class="clearfix"></div>')
            if (finalQuestionCount > 0)
                settings.elements.bodyInfoFeedback.append('<p class="text-center">You got <b>' + finalScore.toFixed(1) + '</b> question(s) correct out of the total <b>'+finalQuestionCount+'</b> markable question(s).</p>')
            settings.elements.bodyInfoFeedback.append('<p class="text-center">Click the review button below to review your selected answers.</p>')
            settings.elements.bodyInfoFeedback.append('<p class="text-center">Click on the reset button below to start the quiz again.</p>')
            settings.elements.bodyInfoFeedback.fadeIn({queue:false, duration:300})
        },

        reset: function() {
            settings.currentQuestion = 1;
            settings.correctCounter = 0;
            settings.partialCounter = 0;
            settings.elements.bodyResetBtn.hide()
            settings.elements.bodyReviewBtn.hide()
            settings.elements.bodyFeedback.html("")
            settings.elements.bodyQuestionWrapper.show()
            settings.elements.main.find(".quiz-check-btn").show()
            settings.elements.bodyBtnGroup.hide()
            settings.elements.bodyCheckBtn.show()
            settings.elements.bodyNextBtn.show()
            if (settings.showBackButton)
                settings.elements.bodyPrevBtn.show()
            methods.updateProgressBar()
            methods.createQuestion()
        },

        shuffle: function(array) {
            var currentIndex = array.length, temporaryValue, randomIndex;
            while (1 !== currentIndex) {
                randomIndex = Math.floor((Math.random() * currentIndex));
                currentIndex -= 1;
                temporaryValue = array[currentIndex];
                array[currentIndex] = array[randomIndex];
                array[randomIndex] = temporaryValue;
            }
            return array;
        },

        review: function() {
            settings.elements.bodyReviewBtn.hide()
            var currentReview = 0;
            settings.elements.bodyFeedback.html("")

            var table = $("<table class='table-responsive table table-bordered table-striped'>")
            var thead = $("<thead><tr><th scope='col' width='33.33%'>Question</th><th scope='col' width='33.33%'>Your Answers</th><th width='33.33%'>Correct Answers</th></thead>")
            var tbody = $("<tbody></tbody>")

            var tr = $("<tr></tr>")
            var td = [];
            $(settings.questions).each(function(i) {
                var wrap = tr.clone()
                var arr = settings.selectedAnswerArr[i]
                if (arr[0].length > 1) {
                    arr = arr.join("")
                }
                if (arr == settings.correctAnswerArr[i]) {
                    wrap.addClass("success")
                }
                else {
                    if (settings.questions[i].type == "textentry") {
                        wrap.addClass("info")
                    }
                    else {
                        wrap.addClass("danger")
                    }
                }
                wrap.append("<td><p><b>Q"+(i+1)+":</b> "+settings.questions[i].question+"</p></td>")
                wrap.append("<td>"+arr+"</td>")
                wrap.append("<td>"+settings.correctAnswerArr[i]+"</td>")
                td.push(wrap)
            })
            table.append(thead)
            table.append(tbody.append(td))
            settings.elements.bodyFeedback.html(table)
        }
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        methods.init(this)
    });
};
