jQuery.fn.vimeoPlaylist = function(options) {
 
    var defaults = {

    };
 
    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() { 
            settings.elements.wrap = $("<div class='videoPlaylist center'>");

            settings.elements.videoWrapper = $("<div class='vimeoVideoPlayer'>");
            settings.elements.videoIntro = $("<div class='col-md-12 introScreen'><p><i class='fa playBtn fa-play-circle fa-4x'></i></p><p>Select a video from the playlist below.</p></div>");
            settings.elements.videoFrame = $("<iframe id='player_"+settings.elements.main.attr('id')+"' src='' class='video videoPlayer' frameborder='0' webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>");
            
            settings.elements.playlistMenu = $("<div class='playlistMenu'>");
            settings.elements.playlistMenuText = $("<i class='fa fa-bars playlistText'> Hide Playlist</i>");
            settings.elements.playlistMenuCounter = $("<div class='videoCounter'>");

            settings.elements.playlistContainer = $("<div class='thumbnailContainer visible'>");
            settings.elements.playlistThumbDiv = $("<div tabindex=0 class='thumbDiv row'>");
            settings.elements.playlistThumbImg = $("<div class='col-md-2 col-sm-4 col-xs-4 thumbImg'><img data-id='' class='noborder transparent videoThumb'/></div>");
            settings.elements.playlistThumbInfo = $("<div class='thumbInfo col-md-10 col-sm-8 col-xs-8'>");

            settings.elements.playlistMenuCounter.html("Currently Viewing Video 0/" + settings.videos.length);
            settings.elements.current = 1;
            methods.buildContainers();
            methods.addEventListener();


        },

        buildContainers: function() {
            settings.elements.main.append(settings.elements.wrap);
            settings.elements.wrap.append(settings.elements.videoWrapper);
            settings.elements.videoWrapper.append(settings.elements.videoFrame);
            settings.elements.wrap.append(settings.elements.playlistMenu);
            settings.elements.playlistMenu.append(settings.elements.playlistMenuText);
            settings.elements.playlistMenu.append(settings.elements.playlistMenuCounter);
            settings.elements.wrap.append(settings.elements.playlistContainer);
            methods.buildThumbnails();
        },

        buildThumbnails: function() {
            for (var i = 0; i < settings.videos.length; i++) {
                settings.elements.playlistContainer.append(settings.elements.playlistThumbDiv.clone().attr("data-num", i));                
            }

            var thumbDiv = settings.elements.main.find($(".thumbDiv"));

            thumbDiv.each(function(i) {
                $(this).append(settings.elements.playlistThumbImg.clone());
                $(this).append(settings.elements.playlistThumbInfo.clone().addClass("ti" + i).attr("data-num", i));
                if ((i % 2) == 0) {
                    $(this).addClass('videoBG1')
                }
                else {
                    $(this).addClass('videoBG2')
                }
            });

            var thumbImg = settings.elements.main.find($(".videoThumb"));
            thumbImg.each(function(i) {
                var ctx = $(this);
                $(this).attr("data-num", (i));
                var host = settings.videos[i].host;

                if (host == "vimeo") {
                    $.ajax({
                        url: 'https://vimeo.com/api/oembed.json?url=https://vimeo.com/' + settings.videos[i].video_id ,
                        dataType: 'json',
                        success:  function(data) {
                            ctx.attr('src', data.thumbnail_url);
                            ctx.css("width", "100%");
                            settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Title:</b> " + data.title + "</p>");
                            settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Duration:</b> " + data.duration + " seconds" + "</p>");
                            if (data.description.length > 0) // If video has description it will be added.
                                settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Description:</b> " + data.description + "</p>");
                        },
                        error: function(){
                            ctx.attr('src', 'data:image/jpeg;base64,');
                            ctx.css("width", "100%");

                            var title =  settings.videos[i].title
                            var duration = settings.videos[i].duration
                            if (typeof title == 'undefined'){
                                title = "N/A"
                            }
                            if (typeof duration == 'undefined'){
                                var duration = "N/A"
                            }
                            
                            settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Title:</b> " + title + "</p>");
                            settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Duration:</b> " + duration + " seconds" + "</p>");
                        }
                    });
                }
                else if (host == "youtube") {
                    //https://stackoverflow.com/questions/22148885/converting-youtube-data-api-v3-video-duration-format-to-seconds-in-javascript-no
                    var convert_time = function(duration) {
                        var a = duration.match(/\d+/g);

                        if (duration.indexOf('M') >= 0 && duration.indexOf('H') == -1 && duration.indexOf('S') == -1) {
                            a = [0, a[0], 0];
                        }

                        if (duration.indexOf('H') >= 0 && duration.indexOf('M') == -1) {
                            a = [a[0], 0, a[1]];
                        }
                        if (duration.indexOf('H') >= 0 && duration.indexOf('M') == -1 && duration.indexOf('S') == -1) {
                            a = [a[0], 0, 0];
                        }

                        duration = 0;

                        if (a.length == 3) {
                            duration = duration + parseInt(a[0]) * 3600;
                            duration = duration + parseInt(a[1]) * 60;
                            duration = duration + parseInt(a[2]);
                        }

                        if (a.length == 2) {
                            duration = duration + parseInt(a[0]) * 60;
                            duration = duration + parseInt(a[1]);
                        }

                        if (a.length == 1) {
                            duration = duration + parseInt(a[0]);
                        }
                        return duration
                    }

                    $.ajax({
                        url: "https://www.googleapis.com/youtube/v3/videos?part=contentDetails,snippet&id="+settings.videos[i].video_id + "&key=AIzaSyD_2c-1Q2RQsURQHHIrotyeI_miY6rgIbc",
                        dataType: "jsonp",
                        success: function (data) {
                            var title = data.items[0].snippet.title;
                            var des = data.items[0].snippet.description;
                            var thumb = data.items[0].snippet.thumbnails.high.url;
                            var dur = convert_time(data.items[0].contentDetails.duration);

                            ctx.attr('src', thumb);
                            ctx.css("width", "100%");
                            settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Title:</b> " + title + "</p>");
                            settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Duration:</b> " + dur + " seconds" + "</p>");
                            if (des.length > 0) // If video has description it will be added.
                                settings.elements.main.find($(".ti" + (i))).append("<p class='thumbInfoText'>" + "<b>Description:</b> " + des + "</p>");
                        }
                    });
                }
            });
            methods.changeVideoSrc(0, false)
        },

        changeVideoSrc: function(thisNum, boo) {
            var currentThumbs = settings.elements.playlistContainer.find($(".thumbDiv"));
            
            settings.elements.main.find($(".videoCounter")).html("Currently Viewing Video " + (thisNum + 1) + "/" + settings.videos.length);
            settings.elements.current = thisNum;

            if (settings.videos[thisNum].host == "vimeo") {
                if (boo == true) {
                    videoSrc = "//player.vimeo.com/video/" + settings.videos[thisNum].video_id + "?api=1&player_id=player_"+settings.elements.main.attr('id')+"&autoplay=1";
                }
                else if (boo == false) {
                    videoSrc = "//player.vimeo.com/video/" + settings.videos[thisNum].video_id + "?api=1&player_id=player_"+settings.elements.main.attr('id')
                }
            }
            else if (settings.videos[thisNum].host == "youtube") {
                if (boo == true) {
                    videoSrc = "https://www.youtube.com/embed/" + settings.videos[thisNum].video_id + "?autoplay=1"
                }
                else if (boo == false) {
                    videoSrc = "https://www.youtube.com/embed/" + settings.videos[thisNum].video_id
                }
            }

            settings.elements.videoFrame.attr("src", videoSrc)
            url = "https://" + settings.elements.videoFrame.attr("src").split('?')[0];
            if ($(".videoPlayer").css("display", "hidden")) {
                $(".videoPlayer").show();
            }
            settings.elements.main.find($(currentThumbs)).removeClass('currentlyPlaying')
            settings.elements.main.find($(currentThumbs[thisNum])).addClass('currentlyPlaying')
        },


        addEventListener: function() {
            var currentPlayBtn = settings.elements.main.find($(".playBtn"));
            var currentThumbs = settings.elements.main.find($(".thumbDiv"));
            var currentMenu = settings.elements.main.find($(".playlistMenu"));

            $(currentThumbs).keydown(function(e) {
                if (e.which == 13) { // If the key pressed was the enter key. View: http://css-tricks.com/snippets/javascript/javascript-keycodes/ - for a list of keycodes
                    methods.changeVideoSrc($(this).data("num"), true);
                }
            });

            $(currentThumbs).click(function() {
                methods.changeVideoSrc($(this).data("num"), true);
            });

            $(currentPlayBtn).click(function() {
                methods.changeVideoSrc(0, true);
            });

            $(currentMenu).click(function() {
                methods.toggleMenu($(this).children().first());
            });


            if (window.addEventListener) {
                window.addEventListener('message', methods.onMessageReceived, false);
            }
            else {
                window.attachEvent('onmessage', methods.onMessageReceived, false);
            }

            var ogColour;
            settings.elements.main.find($(".thumbDiv")).hover(function() {
                $( this ).toggleClass('videoHover')
              }, function() {
                $( this ).toggleClass('videoHover')
              }
            );            
        },

        toggleMenu: function(menu) {
            menu.toggleClass("showHide")
            if (menu.hasClass('showHide'))
                menu.html(" Show Playlist")
            else
                menu.html(" Hide Playlist")

            settings.elements.playlistContainer.slideToggle()
        },

        onMessageReceived: function(e) {
            var data = JSON.parse(e.data);
            if (data.event == 'ready') {
                methods.onReady();
            } else if (data.event == 'finish') {
                methods.onFinish(data.player_id);
            }
        },

        post: function(action, value) {
            var data = {
                method: action
            };
            
            if (value) {
                data.value = value;
            }
            settings.elements.videoFrame[0].contentWindow.postMessage(data, url);
        },

        onReady: function() {
            methods.post('addEventListener', 'finish');
        },

        onFinish: function(id) {
            var videoIdArr = [];
            var thumbDivArr = [];
            settings.elements.main.find($(".videoThumb")).each(function(i) {
                videoIdArr[i] = settings.videos[$(this).data("num")].video_id
                thumbDivArr[i] = $(this).parent().parent()
            });
            if ((settings.elements.current) == settings.videos.length - 1) {
                settings.elements.current = 0;
                if (settings.videos[0].host == "vimeo") {
                    videoSrc = "//player.vimeo.com/video/" + videoIdArr[0] + "?api=1&player_id=player_"+settings.elements.main.attr('id')+"&autoplay=1"
                }
                else if (settings.videos[0].host == "youtube") {
                    videoSrc = "https://www.youtube.com/embed/" + videoIdArr[0] + "?autoplay=1"
                }
                settings.elements.videoFrame.attr("src", videoSrc)
                settings.elements.main.find($(".thumbDiv")).removeClass('currentlyPlaying')
                settings.elements.main.find($(thumbDivArr[0])).addClass('currentlyPlaying')
                settings.elements.playlistMenuCounter.html("Currently Viewing Video " + "1/" + settings.videos.length);
            }
            else {
                settings.elements.current++;
                settings.elements.playlistMenuCounter.html("Currently Viewing Video " + (settings.elements.current + 1) + "/" + settings.videos.length);
                if (settings.videos[settings.elements.current].host == "vimeo") {
                    videoSrc = "//player.vimeo.com/video/" + videoIdArr[settings.elements.current] + "?api=1&player_id=player_"+settings.elements.main.attr('id')+"&autoplay=1"
                }
                else if (settings.videos[settings.elements.current].host == "youtube") {
                    videoSrc = "https://www.youtube.com/embed/" + videoIdArr[settings.elements.current] + "?autoplay=1"
                }
                settings.elements.videoFrame.attr("src", videoSrc)
                settings.elements.main.find($(".thumbDiv")).removeClass('currentlyPlaying')    
                settings.elements.main.find($(thumbDivArr[settings.elements.current])).addClass('currentlyPlaying')    
            }
        },   
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this)
        settings.elements.main.addClass("col-md-12")
        methods.init(this)
    });


 
};