jQuery.fn.showhide = function(options) {

    var defaults = {
        mode: 'preset', // Preset || Custom
        position: 'left', // Left || Right || Top || Bottom
        imageColBool: false,
        imageCols: 3,
        clickableCols: 3,
        textBoxCols: 9,
        instructions: "Click or tap on the triggers to learn more.",
        buttonClass: 'btn-info',
        prefillTextbox: false,
        customTextBox: false,
        buttonHeight: 'auto',
        instructionsInTextBox: true,
        imageMap: false,
    };

    var settings = $.extend( {}, defaults, options );

    var methods = {
        init : function() {
            methods.createElements()
            methods.addContainerClasses()
            methods.buildContainers()
            methods.buildClickables()
            if (settings.imageColBool) {
                methods.buildImage()
            }
            methods.addClickableAttributes()
            methods.bindEvents()
            methods.keyboardAccessibility()
            methods.screenReaderUpdate()
            if (settings.prefillTextbox) {
                methods.prefillTextbox()
            }
            if (settings.mode == 'preset') {
                methods.resizeButtons()
            }
        },

        createElements: function() {
            settings.elements.xsSmCheck = $("<div class='sr-only visible-xs-block'>")
            settings.elements.instructions = $("<p class='col-md-12 text-center'><b tabindex='0' aria-labelledby='showhide-screen-reader-"+settings.elements.main.attr('id')+"' class='showhide-instructions'>"+settings.instructions+"</b></p>")
            settings.elements.imageContainer = $("<div class='showhide-image-container'>")
            settings.elements.clickableContainer = $("<div class='showhide-clickable-container'>")
            settings.elements.textBoxContainer = $("<div class='showhide-textbox-container'>")
            settings.elements.textBox = $("<div class='showhide-textbox clearfix'>")
            settings.elements.screenReader = $("<div aria-live='assertive' id='showhide-screen-reader-"+settings.elements.main.attr('id')+"' class='sr-only'>")
            settings.elements.stage = $("<div class='showhide-stage'>")
            settings.elements.trigger = $("<div class='showhide-trigger'>")
        },

        addContainerClasses: function() {
            if (settings.mode == 'preset') {
                if (settings.position == 'left' || settings.position == 'right') {
                    if (settings.imageColBool) {
                        settings.elements.imageContainer.addClass("col-md-"+settings.imageCols+" col-sm-"+settings.imageCols+" col-xs-12")
                        settings.elements.clickableContainer.addClass("col-md-"+settings.clickableCols+" col-sm-"+settings.clickableCols+" col-xs-12")
                        settings.elements.textBoxContainer.addClass("col-md-"+settings.textBoxCols+" col-sm-"+settings.textBoxCols+" col-xs-12")
                    }
                    else {
                        settings.elements.clickableContainer.addClass("col-md-"+settings.clickableCols+" col-sm-"+settings.clickableCols+" col-xs-12")
                        settings.elements.textBoxContainer.addClass("col-md-"+settings.textBoxCols+" col-sm-"+settings.textBoxCols+" col-xs-12")
                    }
                }
                else if (settings.position == 'top' || settings.position == 'bottom') {
                    settings.elements.textBoxContainer.addClass("col-md-12 col-sm-12 col-xs-12")
                }
            }
            else if (settings.mode == 'custom') {
                settings.elements.textBoxContainer.addClass("col-md-12 col-sm-12 col-xs-12")
            }
        },

        buildContainers: function() {
            settings.elements.main.append(settings.elements.xsSmCheck)
            settings.elements.main.append(settings.elements.screenReader)
            if (!settings.instructionsInTextBox)
                settings.elements.main.append(settings.elements.instructions)
            if (settings.mode == 'preset') {
                if (settings.position == 'left') {
                    if (settings.imageColBool) {
                        settings.elements.main.append(settings.elements.imageContainer)
                        settings.elements.main.append(settings.elements.clickableContainer)
                        settings.elements.main.append(settings.elements.textBoxContainer.append(settings.elements.textBox))
                    }
                    else {
                        settings.elements.main.append(settings.elements.clickableContainer)
                        settings.elements.main.append(settings.elements.textBoxContainer.append(settings.elements.textBox))
                    }
                }
                else if (settings.position == 'top') {
                    settings.elements.main.append(settings.elements.clickableContainer)
                    settings.elements.main.append(settings.elements.textBoxContainer.append(settings.elements.textBox))
                }
                else if (settings.position == 'right') {
                    if (settings.imageColBool) {
                        settings.elements.main.append(settings.elements.textBoxContainer.append(settings.elements.textBox))
                        settings.elements.main.append(settings.elements.clickableContainer)
                        settings.elements.main.append(settings.elements.imageContainer)
                    }
                    else {
                        settings.elements.main.append(settings.elements.textBoxContainer.append(settings.elements.textBox))
                        settings.elements.main.append(settings.elements.clickableContainer)
                    }
                }
                else if (settings.position == 'bottom') {
                    settings.elements.main.append(settings.elements.textBoxContainer.append(settings.elements.textBox))
                    settings.elements.main.append(settings.elements.clickableContainer)
                }
            }
            else if (settings.mode == 'custom') {
                settings.elements.main.append(settings.elements.stage)
                if (settings.customTextBox) {
                    settings.elements.stage.append(settings.elements.textBox)
                    settings.elements.textBox.addClass("show-hide-custom-text-box")
                    settings.elements.textBox.css(settings.customTextBoxCSS)
                }
                else {
                    settings.elements.main.append(settings.elements.textBoxContainer.append(settings.elements.textBox))
                }
                if (settings.imageMap) {
                    var image = $("<img alt='' usemap='#Map-"+settings.elements.main.attr('id')+"' class='img-responsive show-hide-image-map'>");
                    image.attr("src", settings.imageMapSrc)
                    settings.elements.stage.append(image)
                    methods.imageMap()
                    methods.bindImageMap()
                    image.rwdImageMaps()
                    bind()

                    $(window).resize(function() {
                        bind()
                    })

                    function bind() {
                        image.maphilight({
                            fillColor: 'FF7800',
                            fillOpacity: 0.3,
                            stroke: false
                        });
                    }
                }
                else {
                    settings.elements.stage.css(settings.stage.css)
                }
            }

            if (settings.instructionsInTextBox) {
                settings.elements.textBox.html(settings.elements.instructions)
            }
        },

        imageMap: function() {
            var map = $('<map class="show-hide-map" name="Map-'+settings.elements.main.attr('id')+'" id="Map-'+settings.elements.main.attr('id')+'">')
            var coords = [];
            var area = [];

            $(settings.triggers).each(function(i) {
                coords.push(settings.triggers[i].coords)
            })

            $(coords).each(function(i) {
                var length = (coords[i].split(",").length)
                if (length == 3) {
                    area.push($('<area data-content="'+settings.triggers[i].content+'" tabindex="0" class="show-hide-map-area" shape="circle" coords="'+coords[i]+'" />'))
                }
                else {
                    area.push($('<area data-content="'+settings.triggers[i].content+'" tabindex="0" class="show-hide-map-area" shape="poly" coords="'+coords[i]+'" />'))
                }
            })

            map.append(area)
            settings.elements.stage.append(map)
        },

        bindImageMap: function() {
            settings.elements.main.find(".show-hide-map-area").click(function() {
                settings.elements.textBox.html($(this).data("content"))
            })

            settings.elements.main.find(".show-hide-map-area").focus(function() {
                $(this).mouseover()
                $(this).blur(function() {
                    $(this).mouseout()
                })
                $(this).on('keydown', function(e) {
                    if (e.which == 13) {
                        settings.elements.textBox.html($(this).data('content'))
                        methods.screenReaderUpdate($(this).data('content'))
                    }
                })
            })
        },

        buildClickables: function() {
            if (settings.mode == 'preset') {
                var container = settings.elements.clickableContainer;
                for (var i = 0; i < settings.clickables.length; i++) {
                    if (settings.clickables[i].text == undefined) {
                        var div = $("<div>")
                        if (settings.clickables[i].imageAlt == undefined)
                            var img = $("<img alt=''>")
                        else
                            var img = $("<img alt='"+settings.clickables[i].imageAlt+"'>")
                        div.attr("tabindex", "-1")
                        div.addClass("col-md-"+settings.clickables[i].col+" col-sm-"+settings.clickables[i].col+" col-xs-"+settings.clickables[i].col+"")
                        img.addClass("showhide-clickable showhide-clickable-img")
                        img.attr("tabindex", "0")
                        img.attr("src", settings.clickables[i].image)
                        img.addClass("img-responsive img-rounded")
                        div.append(img)
                        container.append(div)
                    }
                    else {
                        if (settings.position == 'left' || settings.position == 'right') {
                            var button = $("<button>")
                            button.attr("tabindex", "0")
                            button.addClass("showhide-clickable showhide-clickable-btn btn")
                            button.addClass(settings.buttonClass)
                            button.addClass("col-md-"+settings.clickables[i].col+" col-sm-"+settings.clickables[i].col+" col-xs-12")
                            button.html(settings.clickables[i].text)
                            button.css("height", settings.buttonHeight)
                            container.append(button)
                        }
                        else {
                            var div = $("<div>")
                            var button = $("<button>")
                            div.attr("tabindex", "-1")
                            div.addClass("showhide-clickable col-md-"+settings.clickables[i].col+" col-sm-"+settings.clickables[i].col+" col-xs-12")
                            div.css("height", settings.buttonHeight)
                            button.addClass("showhide-clickable-btn showhide-clickable btn")
                            button.addClass(settings.buttonClass)
                            button.html(settings.clickables[i].text)
                            div.append(button)
                            container.append(div)
                        }
                    }
                }
            }
            else if (settings.mode == 'custom' && !settings.imageMap) {
                for (var i = 0; i < settings.triggers.length; i++) {
                    settings.elements.stage.append(settings.elements.trigger.clone().attr('data-num',i))
                }
            }
        },

        buildImage: function() {
            if (settings.clickables[0].imageColAlt == undefined) {
                settings.elements.imageContainer.append("<img tabindex='0' class='img-responsive img-rounded' alt='' src="+settings.clickables[0].imageColSrc+">")
            }
            else {
                settings.elements.imageContainer.append("<img tabindex='0' class='img-responsive img-rounded' alt='"+settings.clickables[0].imageColAlt+"' src="+settings.clickables[0].imageColSrc+">")
            }
        },

        addClickableAttributes: function() {
            if (settings.mode == 'preset') {
                var container = settings.elements.clickableContainer;

                settings.elements.main.find('.showhide-clickable-btn').each(function(i) {
                    $(this).attr('data-content',settings.clickables[i].content)
                    $(this).attr("data-num", i)
                })

                settings.elements.main.find('.showhide-clickable-img').each(function(i) {
                    $(this).attr('data-content',settings.clickables[i].content)
                    $(this).attr("data-num", i)
                })

                container.children()
                    .attr('aria-labelledby', "showhide-screen-reader-"+settings.elements.main.attr('id')+"")
            }
            else if (settings.mode == 'custom') {
                settings.elements.main.find('.showhide-trigger').each(function(i) {
                    $(this).css(settings.triggers[i].css)
                    $(this).attr('data-content', settings.triggers[i].content)
                    $(this).attr('tabindex', '0')
                    $(this).attr('aria-labelledby', "showhide-screen-reader-"+settings.elements.main.attr('id')+"")
                })
            }
        },

        setTextBoxHeight: function(boo) {
            settings.elements.textBox.css('height', 'auto')

            if (settings.mode == 'preset') {
                if (settings.position == 'left' || settings.position == 'right') {
                    if (settings.elements.xsSmCheck.css('display') == 'block') {
                        settings.elements.textBox.removeClass('showhide-textbox-leftright')
                        getHeight(boo)
                    }
                    else {
                        settings.elements.textBox.css('height', settings.elements.clickableContainer.height() - 5)
                        settings.elements.textBox.addClass('showhide-textbox-leftright')
                    }
                }
                if (settings.position == 'top' || settings.position == 'bottom') {
                    getHeight(boo,"clickables")
                }
            }
            else if (settings.mode == 'custom') {
                getHeight(boo,'triggers')
            }
            function getHeight(boo, type) {
                var largest = 0;

                var originalContent = settings.elements.textBox.html()
                settings.elements.textBox.css('height', 'auto')
                for (var i = 0; i < eval("settings."+type+".length"); i++) {
                    settings.elements.textBox.html(eval("settings."+type+"[i].content"))
                    settings.elements.textBox.children().css('visibility','hidden')
                    if (settings.elements.textBox.find('img').attr('src') != undefined && boo == false) {
                        var current = i;

                        var image = new Image();

                        image.src = settings.elements.textBox.find('img').attr('src');

                        image.onload = function() {
                            settings.elements.textBox.css('height', 'auto')
                            settings.elements.textBox.html(eval("settings."+type+"[current].content"))

                            if (settings.elements.textBox.innerHeight() > largest)
                                largest = settings.elements.textBox.innerHeight()
                                setHeight()
                        }
                    }
                    else {
                        if (settings.elements.textBox.innerHeight() > largest)
                            largest = settings.elements.textBox.innerHeight()
                    }
                }
                setHeight()
                function setHeight() {
                    settings.elements.textBox.html(originalContent)
                    settings.elements.textBox.css('height', largest)
                }

            }

        },

        bindEvents: function() {
            settings.elements.main.find('.showhide-clickable').on('click', function() {
                settings.elements.main.find(".showhide-clickable-btn").removeClass("selected")
                $(this).children().addClass("selected")
                $(this).addClass("selected")
                settings.elements.textBox.html($(this).data('content'))
                methods.screenReaderUpdate($(this).data('content'))
                if (settings.imageColBool) {
                    settings.elements.imageContainer.children().attr("src",settings.clickables[$(this).data('num')].imageColSrc)
                    settings.elements.imageContainer.children().attr("alt",settings.clickables[$(this).data('num')].imageColAlt)
                }
            })

            settings.elements.main.find('.showhide-trigger').on('click', function() {
                settings.elements.textBox.html($(this).data('content'))
                methods.screenReaderUpdate($(this).data('content'))
                if (settings.triggers[$(this).data('num')].stageBgImg != undefined) {
                    var ctx = $(this)
                    $('<img src="'+ settings.triggers[$(this).data('num')].stageBgImg.split('url(')[1].split(')')[0] +'">').load(function() {
                        settings.elements.stage.css('backgroundImage',settings.triggers[ctx.data('num')].stageBgImg)
                    });
                }
            })

            settings.elements.main.find('.showhide-instructions').on('focus', function() {
                methods.screenReaderUpdate("Navigate to each of the buttons and click on them to learn more.")
            })

            settings.elements.main.find('.showhide-clickable').on('focus', function() {
                methods.screenReaderUpdate($(this).html())
            })

            settings.elements.main.find('.showhide-trigger').on('focus', function() {
                methods.screenReaderUpdate("Click this to learn more")
            })
        },

        keyboardAccessibility: function() {
            settings.elements.main.find('.showhide-clickable').on('keydown', function(e) {
                if (e.which == 13) {
                    settings.elements.textBox.html($(this).data('content'))
                    methods.screenReaderUpdate($(this).data('content'))
                }
            })
            settings.elements.main.find('.showhide-trigger').on('keydown', function(e) {
                if (e.which == 13) {
                    settings.elements.textBox.html($(this).data('content'))
                    methods.screenReaderUpdate($(this).data('content'))
                }
            })
        },

        screenReaderUpdate: function(content) {
            settings.elements.screenReader.html(content)
        },

        scrollFix: function() {
            settings.elements.main.find('.showhide-textbox').bind( 'mousewheel DOMMouseScroll', function ( e ) {
                var e0 = e.originalEvent,
                    delta = e0.wheelDelta || -e0.detail;

                this.scrollTop += ( delta < 0 ? 1 : -1 ) * 30;
                e.preventDefault();
            });
        },

        prefillTextbox: function() {
            if (settings.mode == "custom") {
                settings.elements.textBox.html(settings.triggers[0].content)
            }
            else {
                settings.elements.main.find(".showhide-clickable-btn").first().addClass("selected")
                settings.elements.textBox.html(settings.clickables[0].content)
            }
        },

        resizeButtons: function() {
            function resize() {
                settings.elements.main.find(".showhide-clickable").children().css("height", "auto")
                var largest = 0;
                settings.elements.main.find(".showhide-clickable").each(function(i) {
                    if ($(this).height() > largest) {
                        largest = $(this).height();
                    }
                })
                settings.elements.main.find(".showhide-clickable").children().css("height", largest)
            }
            resize()
            $(window).resize(function() {
                resize()
            })
        }
    }

    return this.each(function() {
        settings.elements = {}
        settings.elements.main = $(this).addClass('showhide-activity-wrapper')
        methods.init(this)
    });
};
